.silver {
  @include organism__container;
  margin-bottom: $slice-vertical-spacing;

  .silver__list-item {
    vertical-align: top;

    @include mq(g3) {
      display: flex;
    }
  }

  @include mq(g3) {
    display: flex;

    .metadata {
      display: flex;
      position: absolute;
      bottom: 0;
    }
    .headlines__list {
      .metadata {
        display: block;
        position: relative;
      }
      .promo__body {
        padding-bottom: 8px;
      }
    }
    .promo__body {
      padding-bottom: 24px;
    }
  }

  .silver__list {
    @include mq(g3) {
      display: flex;
      flex-wrap: wrap;
    }
    &.row {
      margin-bottom: 16px;

      .metadata {
        position: relative;
      }
    }
  }
  .promo .promo__container {
    opacity: 1;
  }
  @include mq(g5) {
    .g5__1col {
      width: 20%;
      display: flex;
    }
    .g5__4cols {
      width: 80%;
      display: flex;
    }

    .promo--hero{
      h2{padding-top:16px;}
    }
  }

  @include mq($from:g4, $until:g5) {
    .promo-core--lead .metadata {
      position: relative;
    }
    .g4__3cols {
      width: 75%;
      display: flex;
    }
    .g4__1col {
      width: 25%;
      display: flex;
    }
    .g4__4col {
      width: 100%;
      display: flex;
    }
    .g4__mt-12 {
      margin-top: 12px;
    }

  }
  @include mq($from:g3, $until:g4) {
    .g3__2cols {
      width: 66.666%;
      display: flex;
    }
    .g3__1col {
      width: 33.333%;
      display: flex;
    }
    .g3__3cols {
      width: 100%;
      display: flex;
    }
    .g3__mt-12 {
      margin-top: 12px;
    }
  }
  @include mq($from:g1, $until:g3) {
    .silver__list.row {
      margin-bottom: 8px;
    }
    .promo--hero {
      margin-bottom: 24px;
    }
  }
}
.headlines__list {
  .headlines__list--item:first-child .promo {
    @include mq(g3) {
      padding-top: 0;

      &:after {
        content: none;
      }
    }
  }
  .headlines__list--item:last-child .promo {
    @include mq(g4) {
      padding-bottom: 0;
    }
  }
  .promo {
    padding-top: 12px;
    padding-bottom: 12px;
    position: relative;
  }

  .promo:after {
    position: absolute;
    right: 0;
    top: 0;
    left: 0;
    display: block;
    border: 0 solid #dbdbdb;
    border-top-width: 1px;
    content: '';
    z-index: 999;
  }

  .promo__body {
    padding-bottom: 8px;
  }

  @include mq($from:g4, $until:g5) {
    &.g4__h-horizontal {
      position: relative;
      width: 100%;

      &:after {
        position: absolute;
        right: 0;
        top: 0px;
        left: $g4-gutter-width / 2;
        display: block;
        border: 0 solid #dbdbdb;
        border-top-width: 1px;
        content: '';
        z-index: 999;

        width:calc(100% - #{
          $g4-gutter-width
        }
        )
      }
      .headlines__list--item {
        .promo:after {
          content: none;
        }
        width: 25%;
        display: inline-block;
      }
    }
  }
  @include mq($from:g3, $until:g4) {
    &.g3__h-horizontal {
      position: relative;
      width: 100%;

      &:after {
        position: absolute;
        right: 0;
        top: 0;
        left: $g3-gutter-width / 2;
        display: block;
        border: 0 solid #dbdbdb;
        border-top-width: 1px;
        content: '';
        z-index: 999;

        width:calc(100% - #{
          $g3-gutter-width
        }
        )
      }
      .headlines__list--item {
        .promo:after {
          content: none;
        }
        width: 33.333%;
        display: inline-block;
      }
    }
  }
}
