$g5-gutter-width: 24px;
$g1-gutter-width: 8px;
$g2-gutter-width: 16px;
$g3-gutter-width: 16px;
$g4-gutter-width: 20px;
$gutter-width: 16px;
$list-item-top-margin:16px;
$gridContainerPadding:4px;
$g2-image-width-small: 142px;
$g2-image-width-large: 142px;
$vertical-spacing: 24px;
$slice-vertical-spacing: 32px;
$list-min-height: 75px;

@import 'older_sass/normalize';
@import 'older_sass/mq';
@import 'older_sass/mixins';
@import 'older_sass/flexbox';

// ATOMS
//
@import 'older_sass/atoms';
@import 'older_sass/molecules';
@import 'older_sass/organisms';
@import 'older_sass/animations';
@import 'organisms/oxygen';
@import 'organisms/fluorine';
@import 'organisms/carbon';
@import 'atoms/topic__tag';
@import 'molecules/topic__tags__molecule';
@import 'molecules/promo';
@import 'molecules/promoLead';
@import 'molecules/radium';
@import 'molecules/radon';
@import 'molecules/reporters';
@import 'organisms/nitrogen';
@import 'organisms/silver';
@import 'organisms/tin';
@import 'organisms/aluminium';
@import 'organisms/calcium';
@import 'organisms/zinc';
@import 'organisms/gold';
@import 'organisms/bronze';
@import 'organisms/krypton';
@import 'organisms/iron';
@import 'organisms/helium';
@import 'organisms/terbium';
@import 'organisms/carousel';
@import 'organisms/lead';
@import 'organisms/mostPopular';
@import 'organisms/navigation';
@import 'older_sass/visibility-classes';



body.news{
  background-color: #fff;
}


// VARIABLES
div[id*='Block'] {

.dropdown{
    display: none;
    position: absolute;
    z-index: 999;
}
    &:hover .dropdown{
        display: block;
    }
}

//CARDS layout
$cards-spacing-vertical: 16px;
$cards-spacing-horizontal: 16px;
$cards-spacing: 16px;


// COLLIDER
//

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  @include ff-arial;

  color: $body-copy;
}

html,
body {
  height: 100%;
}

p{margin-top: 16px; margin-bottom: 0;}
h1 { @extend %trafalgar; margin: 0;}
h2 { font-size:22px; margin:0;}
h3 { @extend %great-primer; }
h4 { @extend %pica;}

h1,
.alpha {

  // Only apply these styles to Collider heading's
  &.cldr__h {
    letter-spacing: -1px;
  }
}


h1,
.alpha,
h2,
h3,
h4 {
margin-top: 0;
margin-bottom: 0;
  // Only apply these styles to Collider heading's
  &.cldr__h {
    color: $news;
    font-weight: normal;
  }
}

ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
.news .faux-block-link--hover .faux-block-link__target{
  text-decoration: none;
}

.news .faux-block-link__target:hover .faux-block-link__target a,
.news .faux-block-link--hover .faux-block-link__target {
     color: $link-hover;
  text-decoration: none;
}


 a {
  color: $link;
  text-decoration: none;

  &:hover {
    color: $link-hover;
    text-decoration: none;
  }
}

// Sidebar
//

.cldr__sidebar {
  background-color: #FFF;
  height: 100%;
  padding-top: 24px;
  padding-right: 12px;
  padding-left: 12px;
  position: fixed;
    top: 0; left: 0;
  transform: translateX(-240px);
  transition: transform 150ms ease-in;
  width: 240px;
  z-index: 10;

  @include mq(g3) {
    transform: translateX(0);
    transition: transform 250ms ease-out;
  }
}
  .cldr__title {
    @extend .alpha;
    border-bottom: 1px solid #EEE;
    display: block;
    font-size: 2em;
    margin-bottom: 8px;
    padding-bottom: 8px;
  }
  .cldr__primary-nav {
    @include cldr-nav;
    margin-top: 0;
    margin-bottom: 0;

    li {}
    a {
      padding-top: 8px;
      padding-bottom: 8px;
    }
  }



// Content
//

.cldr__content {
  margin-left: 0;
  padding: 24px;

  @include mq(g3) {
    margin-left: 240px;
  }

  > h1 {
    margin-top: 0;
  }
}

.cldr__atom {}
  .cldr__atom__view {}

.cldr__molecule {}

.cldr__atom,
.cldr__molecule {
  margin-bottom: 48px;

  > p {
    color: $news;
  }
}

  .cldr__molecule__view { padding-top: 16px;}
    .cldr__molecule__view__body {}

// Objects
//

.cldr__tabs {
  @include cldr-nav;
  @include cldr-nav--inline;

  li {
    background-color: #EEE;
  }
  .cldr__tab--active {
    background-color: #FFF;
  }
  a {
    padding: 8px 12px;

    &:hover {
      background-color: #FFF;
    }
  }
}

  .cldr__tab-panels {}
    .cldr__tab-panel {
      display: none;
    }
    .cldr__tab-panel--active {
      display: block;
    }


// ATOMS
// - Grid

.cldr__grid {}
  .cldr__grid__item {
    margin-bottom: 8px;

    @include mq(g3) {
      margin-bottom: 16px;
    }

    &:last-of-type {
      margin-bottom: 0;
    }
}
    .cldr__grid__item__text {
      @extend %minion;
      color: rgba(255,255,255,0);
      display: block;
      background-color: #AADBF5;
      padding-top: 2px;
      padding-bottom: 2px;
      text-align: center;
    }


// - Colour

.cldr__colour {
  @include cldr-nav;
  @include cldr-nav--inline;

  li {
    margin-right: 16px;
  }
}
  .cldr__colour__block {
    border: 1px solid #EEE;
    height: 72px;
    position: relative;
    width: 128px;

    span {
      position: absolute;
        left: 0; top: 50%;
      text-align: center;
      transform: translateY(-50%);
      width: 100%;
    }
  }
  .cldr__colour__name {
    margin-top: 8px;
  }
  .cldr__colour__var {
    color: #999;
    margin-top: 4px;
  }

  // atoms/ - Colour Palette blocks
  // - Set the BG colour of the block using our ATOM VARIABLE from _atoms.scss, and type color accordingly.
  .cldr__colour__block--news       { background-color: $news;       color: #FFF; }
  .cldr__colour__block--newsbeat   { background-color: $newsbeat;   color: #FFF; }
  .cldr__colour__block--body-copy  { background-color: $body-copy;  color: #FFF; }
  .cldr__colour__block--link       { background-color: $link;       color: #FFF; }
  .cldr__colour__block--link-hover { background-color: $link-hover; color: #FFF; }


// - Typography
// -- Fonts

.cldr__fonts {
  @include cldr-nav;
}
  .cldr__fonts__font {

    > span {
      font-size: 1.17em;
    }
    > ul {
      @include cldr-nav;
      @include cldr-nav--inline;
      margin-top: 1em;
    }
  }
    .cldr__fonts__font__variation {
      margin-right: 16px;

      p {
        background-color: #111;
        color: #FFF;
        margin-top: 0;
        padding-top: 4px;
        padding-bottom: 4px;
        text-align: center;
      }
    }
      .cldr__fonts__font__specimen {
        background-color: #FFF;
        font-size: 36px;
        height: 128px - 18;
        position: relative;
        width: 72px;

        span {
          position: absolute;
            left: 0; top: 16px;
          text-align: center;
          width: 100%;
        }

        .cldr__fonts__font--arial   & { @include ff-arial; }
        .cldr__fonts__font--georgia & { @include ff-georgia; }

        .cldr__fonts__font__variation--bold   & { @include bold; }
        .cldr__fonts__font__variation--italic & { @include italic; }
      }

// -- Scale

.cldr__typographic-scale {
  width: 100%;

  tr {
    border-top: 1px solid #EEE;
  }
  td {
    padding-top: 8px;
    padding-bottom: 16px;
  }
}
  .cldr__typographic-scale__name     {
    font-size: 13px;
    text-transform: uppercase;
    width: 15%;
  }
  .cldr__typographic-scale__fs       {}
  .cldr__typographic-scale__lh       {}
  .cldr__typographic-scale__lts      {}
  .cldr__typographic-scale__specimen {
    padding-left: 5%;
  }

  .cldr__typographic-scale__name,
  .cldr__typographic-scale__fs,
  .cldr__typographic-scale__lh,
  .cldr__typographic-scale__lts {
    vertical-align: top;
  }

  .cldr__typographic-scale__fs,
  .cldr__typographic-scale__lh,
  .cldr__typographic-scale__lts {
    @include cldr-ff-monaco;
    text-align: right;
    width: 5%;
  }

  .cldr__typographic-scale__specimen--double-trafalgar       { @extend %double-trafalgar; }
  .cldr__typographic-scale__specimen--double-trafalgar--bold { @extend %double-trafalgar--bold; }

  .cldr__typographic-scale__specimen--trafalgar              { @extend %trafalgar; }
  .cldr__typographic-scale__specimen--trafalgar--bold        { @extend %trafalgar--bold; }

  .cldr__typographic-scale__specimen--double-pica            { @extend %double-pica; }
  .cldr__typographic-scale__specimen--double-pica--bold      { @extend %double-pica--bold; }

  .cldr__typographic-scale__specimen--great-primer           { @extend %great-primer; }
  .cldr__typographic-scale__specimen--great-primer--bold     { @extend %great-primer--bold; }

  .cldr__typographic-scale__specimen--pica                   { @extend %pica; }
  .cldr__typographic-scale__specimen--pica--bold             { @extend %pica--bold; }

  .cldr__typographic-scale__specimen--long-primer            { @extend %long-primer; }
  .cldr__typographic-scale__specimen--long-primer--bold      { @extend %long-primer--bold; }

  .cldr__typographic-scale__specimen--brevier                { @extend %brevier; }
  .cldr__typographic-scale__specimen--brevier--bold          { @extend %brevier--bold; }

  .cldr__typographic-scale__specimen--minion                 { @extend %minion; }
  .cldr__typographic-scale__specimen--minion--bold           { @extend %minion--bold; }

// - Lists
// -- Nav
.cldr__nav {
  @include nav;
}
  .cldr__nav--stacked {
    @include nav;
    @include nav--stacked;
  }


// - Iconography
// -- Icons

.cldr__icons {}
  .cldr__icon {}

// -- Branch

.cldr__branch {
  @include branch;
}


// MOLECULES
// - Units

// -- Promo

.cldr__molecule__view__body--promo {
  @extend .one-whole;
  @extend .g3--one-half;
  @extend .g4--one-third;
  @extend .g5--one-quarter;
}


//4 Equal Columns
@include mq($from: g4, $until:g5){

	section.gold{
		.boron{
			width: 100%;
			.boron__list-item{
				    flex-basis: 25%;

			}
		}
		.antimony{
			width: 20%;
			overflow: hidden;
		}
	}
}

//5 Equal Columns
section{display: -webkit-box;}
@include mq(g5){
section.tin{
	.boron {
    width: 60%;
}
	.antimony, .silicon{
		 width: 20%;
	}

}

	section.carbon.hero__horizontal{

			 .carbon__item:nth-child(2){
		    flex-basis: 60%;
	}
		.carbon__item:nth-child(3),.carbon__item:nth-child(4){
			 flex-basis: 20%;
		}
	}

	section.terbium {
	.first-column{
		width: 60%;
	}
		.second-column{
			width: 40%;
		}
}

	section.aluminium{
		.boron{
			width: 60%;
		}
		.antimony {
    width: 40%;

}
	}

	section.gold{
		.boron{
			width: 100%;
			.boron__list-item{
				    flex-basis: 20%;

			}
		}
		.antimony{
			width: 20%;
			overflow: hidden;
		}
	}
}


