.mostPopular{

  padding-bottom: 16px;
  .item__title{overflow:hidden; padding-bottom:24px;}

@include organism__container;
		.mostPopular__container{ width: 100%;}
	.mostPopular__content{margin-top: 16px;}
	.headlines__column{
    @include mq(g5) {
				 width: 100%;
        }
        @include mq($from:g4, $until:g5) {
          width: 100%;
        }
				 display: inline-block;
	}
	.item__title{
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
  }
	.headline-list__count {
    color: $news;
    width: 40px;
    height: 40px;
    float: left;
    position: absolute;
    font-size: 32px;
    letter-spacing: -2px;
    vertical-align: middle;
    top: 0%;
    text-align: center;
    border-radius: 50%;
    font-weight: 100;
  }
  .number{
    vertical-align: middle;
    display: block;
    position: absolute;
    top: -8px;
    width: 38px;
		}
  }
	.headline-list{
overflow: hidden;
   column-count: 5;
		   column-gap: 0;
		    column-fill: balance;
	}
	.headline-list__item{
    &:first-child .promo{background-color:#fff;
      .promo__image-16x9 {
    padding-bottom: 30%;
  }
    .headline-list__text:after{
      // content: none;
    }
    }
     &:nth-child(2),&:nth-child(4), &:nth-child(6),&:nth-child(8), &:last-child{
    .headline-list__text:after{
      content: none;
    }
  }
    &:nth-child(2), &:nth-child(6){
      // -webkit-column-break-before: always;
      // break-before: column;

    }
	    -webkit-column-break-inside: avoid;
      page-break-inside: avoid;
      break-inside: avoid;
		  height: 100%;
		  position: relative;
	    padding-top: 0px;
      padding-bottom: 0;

		.headline-list__text{
      margin-left: 48px;
      &:after{
        content: "";
        display: block;
        height: 1px;
        width: calc(100%);
        background-color: $keyline;
        position: relative;
        top: -12px;
      }
    }

.promo__body{position:relative;}


}
