
section{
.top-list__list{
		@include mq($from: g3, $until:g4){
			margin-bottom: $vertical-spacing;
	&:before{content: ''; width: 100%; height: 1px; background-color: $keyline; display: block; position: absolute; top: 0;}
				position: relative;
		}
width: 100%;
	.top-list-item{
	display: inline-block;
		position: relative;
			width: 100%;
margin: 0;
			margin-bottom: $vertical-spacing;;
text-align: center;

		@include mq($from: g3, $until:g5){
			.avatar{float: none;	width: 25%;margin: 0 auto 8px auto;}
			width: 33.333%;
			margin: 24px 0 0 0 ;
			    vertical-align: top;
			text-align: center;
			+ .top-list-item{
				padding-left: 16px;
			}
		}
	}
	.top-list-item__bullet{
		display: block;
	}
	h3.top-list-item__headline{
	margin-top: 8px;

		@extend %english;
	letter-spacing: -0.5px;

	}
	.top-list-item__byline{
		display: block;
font-style: italic;
font-family: "Georgia",Times,serif;
font-weight: normal;
margin-top: 0.25rem;
font-size: 0.875rem;
	}
	.avatar{
    width: 33%;
    margin-right: 16px;
    border-radius: 50%;
    margin: 0 auto;
		.promo__image-square{
		background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
			padding-bottom: 100%;
					border-radius: 50%;
		}
	}

}
}
