.radium{
    width: 100%;
    .radium__list-item{
        .promo{
                min-height: inherit !important;
            padding-bottom: 0px !important;
            .metadata{
                position: relative !important;
            }
        }
        @include mq(g1){
        &:first-child{margin-top: 0;
            .promo{
                &:before{
                    content: none;
                 @include mq($from:g1, $until:g3){
                content: '';
                }
           
            }
        }
        }
          margin-top: $list-item-top-margin;
        h3{ @extend %english, %english--bold;}
          @include promo-settings(
          /*PSEUDO CLASS (after/before) ----*/before,
        /*DIVIDER CONTENT ----------------*/ '' ,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
        /*IMAGE WIDTH -------------------*/   100%,
        /*IMAGE PADDING -----------------*/   0px 0 0 0px,
        /*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
        /*PROMO BODY PADDING ------------*/   0,
        /*METADATA PADDING --------------*/   0,
        /*SEE ALSO PADDING --------------*/   8px 0
        );
        }
    }
}