.carousel + .carbon {
  margin-top: 0;
}
@include mq(g1) {
  .carbon + .lead, .carbon + .mostPopular, .carbon + .helium, .carbon + .gold, .carbon + .aluminium, .carbon + .tin, .carbon + .terbium, .carousel + .terbium {
    > div[class^="__container"], > div[class*="__container"] {
      margin-top: 0;
    }
    .strapline {
      &:before {
        content: none;
      }
    }
  }
}

.carbon {
    margin-bottom: $slice-vertical-spacing / 2;
  &.standard {
    background-image: url("http://ichef.bbci.co.uk/images/ic/1600xn/p02b2fkf.jpg");
  }
 @include organism__container;
  position: relative;
  background-color: #F9F9F9;
  background-size: cover;
  background-position: top center;

  &:before {
    content: '';
    display: block;
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .strapline {

    &:before {
      content: none
    }
    width:100%;
    color: #000;

    p {
      -webkit-font-smoothing: antialiased;
    }
  }
  .carbon__content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: row;
    align-content: stretch;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: stretch;
    align-content: stretch;
  }

  .carbon__item {
    .promo {
      h3 {
        @extend %paragon;
      }
      h3.double-pica {
        @extend %double-pica;
      }
      &.in-view {
        transform: translateY(0);
        opacity: 1;
      }
      animation-delay: 0.4s;
      opacity: .001;
      transform: translateY(190px);
      transition: opacity .6s ease, transform .6s, ease;
    }
    position: relative;
    display: flex;
    width: 100%;
    @include gutter($gutter-width);

    @include mq(g1) {
      @include horizontal__promo(left, 2, 8px);
      .image {
        margin-bottom: 0;
      }
      .metadata {
        display: inline-block;
      }
      .promo__body {
        padding: 8px 8px 8px 8px;
      }
    }
    @include mq(g2) {
      .topic-tags {
        padding-left: 8px;
        padding-right: 8px;
      }
    }
    @include mq(g3) {
      margin-bottom: $vertical-spacing;
      flex-basis: 50%;

      &:first-child {
        margin-bottom: 0;
      }
      .topic-tags {
        padding-left: 16px;
        padding-right: 16px;
      }
      @include horizontal__promo(none, 1, 16px);

      &:before {
        right: 0;
        opacity: 0.4;
        content: none;
      }
      .metadata {
        position: absolute;
        bottom: 12px;
        left: 12px;
      }
      .promo__body {
        padding: 12px 16px 16px 16px;
      }
      .promo__content {
        position: inherit;
      }
    }
    @include mq(g4) {
      flex-basis: 33.333333333%;
    }
    @include mq(g5) {
      flex-basis: 25%;

      .topic-tags {
        padding-left: 0px;
        padding-right: 0px;
      }
    }
    &:last-child {
      &:before {
        content: none;
      }
      @include mq(g3) {
        flex-basis: 50%
      }
      @include mq(g4) {
        flex-basis: 100%
      }
      @include mq(g5) {
        flex-basis: 25%;
      }
      .promo {
        background-color: transparent;
        padding-bottom: 0;
      }
    }
    &:first-child {
      &:before {
        content: none;
      }
      @include mq(g1) {
        width: calc(4.166666667% * 24);
      }
      @include mq(g3) {
        flex-basis: 100%
      }
      @include mq(g4) {
        flex-basis: 100%;
      }
      @include mq(g5) {
      }
    }
  }
  .promo {
    background: #fff;
    position: relative;
    box-shadow: 0 2px 0px rgba(0, 0, 0, 0.15);

    &:before {
      content: '';
      height: 100%;
      width: 1px;
      background-color: rgba(0, 0, 0, 0.09);
      position: absolute;
      left: -1px;
      display: block;
    }
    &:after {
      opacity: 1;
      content: '';
      height: 100%;
      width: 1px;
      background-color: rgba(0, 0, 0, 0.09);
      position: absolute;
      right: -1px;
      display: block;
      top: 0;
    }
    @include mq(g1) {
      padding-bottom: 0px;
    }
    @include mq(g3) {
      padding-bottom: 24px;
    }
  }
  &.hero {
    .carbon__item {
      .promo__body {
        padding: 8px 12px 16px 12px;
      }
      @include mq(g3) {
        &:nth-child(2) {
          flex-basis: 100%;
          @include horizontal__promo(left, 2, 0px);

          .promo {
            padding-bottom: 0px;
          }
        }
        &:nth-child(3), &:nth-child(4) {
          flex-basis: 50%;
        }
        &:last-child {
          flex-basis: 100%;
        }
      }
      @include mq(g4) {
        &:nth-child(2) {
          flex-basis: 50%;
          @include horizontal__promo(none, 1, 8px);
        }
        &:nth-child(3), &:nth-child(4) {
          flex-basis: 27.8%;
        }
      }
      @include mq(g5) {
        &:nth-child(2) {
          flex-basis: 37.4%;

          .promo__body {
            padding-top: 6px;
          }
          .promo {
            padding-bottom: 24px;
          }
        }
        &:nth-child(3), &:nth-child(4) {
          flex-basis: 18.8%;
        }
        &:last-child {
          flex-basis: 25%;
        }
      }
    }
  }
  &.hero__horizontal {
    .carbon__item {
      .promo__body {
        padding: 8px 12px 16px 12px;
      }
      @include mq(g1) {
        &:nth-child(2) {
          .promo__body {
            padding-bottom: 24px;
          }
          @include horizontal__promo(none, 1, 0px);
          flex-basis: 100%;

          .metadata {
            left: 12px;
          }
        }
        .metadata {
          position: relative;
          bottom: 6px;
          left: 8px;
        }
        &:last-child {
          .promo {
            background-color: #fff;
          }
        }
      }
      @include mq(g3) {
        &:nth-child(2) {
          .promo {
            padding-bottom: 0;
          }
          flex-basis: 100%;

          .image {
            width: 66.666%;
            float: left;
            margin: 0 12px 0 0;
          }
          .metadata {
            position: relative;
          }
        }
        &:nth-child(3), &:nth-child(4) {
          flex-basis: 50%;
        }
        .metadata {
          position: absolute;
          bottom: 6px;
          left: 8px;
        }
      }
      @include mq(g4) {
        &:nth-child(2) {
          flex-basis: 50%;
          @include horizontal__promo(none, 1, 0px);

          .promo {
            padding-bottom: 8px;
          }
          .metadata {
            left: 12px;
            position: absolute;
            bottom: 8px;
          }
          .promo__body {
            padding-top: 6px;
          }
        }
        &:nth-child(3), &:nth-child(4) {
          flex-basis: 25%;

          .metadata {
            left: 12px;
          }
        }
      }
      @include mq(g5) {
        &:nth-child(2) {
          flex-basis: 56.2%;

          .image {
            margin-right: 0;
          }
          @include horizontal__promo(right, 1.5, 0px);

          .promo {
            padding-bottom: 0px;
          }
          .metadata {
            position: absolute;
            padding-top: 16px;
          }
        }
        &:nth-child(3), &:nth-child(4) {
          flex-basis: 21.9%;
        }
        &:last-child {
          flex-basis: 21.9%;
        }
      }
      @include mq($from:g1, $until:g3) {
        .image {
          width: 150px;
        }
      }
      @include mq($from:400px, $until:g3) {
        .metadata {
          position: absolute;
          bottom: 6px;
          left: 150px;
        }
      }
    }
  }
}
