.tin {
  @include organism__container;

  .tin__container {
    display: table;

    .promo {
      position: relative;
    }
  }
  @include mq(g1) {
     .radon__list-item {
      margin-bottom: $vertical-spacing;
     }
    .neon {
      width: 100%;
    }
    .boron {
      .boron__list-item {
        &:first-child {
          h3 {
            @extend %double-pica, %double-pica--bold;
            letter-spacing: -1px;
          }
        }
      }
    }
    .tin__container {
      display: table;
    }
    .tin__list {
      @include flexbox;
      position: relative;
      @include flex-flow(row wrap);
    }

    .antimony {
      h3 {
        @extend %brilliant;
        font-weight: 700;
        letter-spacing: 0;
      }
      .antimony__list-item {
        width: 100%;
      }
    }
  }
  //END GROUP 1

  @include mq(g3) {


    .promo .promo__body {
      padding-bottom: $vertical-spacing;
    }

    .boron {
      .boron__list-item {
        &:first-child {
          h3 {
            @extend %double-pica, %double-pica--bold;
            letter-spacing: -1px;
          }
          @include flex-basis(100%);

          .promo__image-wrapper {
            float: right;
            width: 66.6666%;
            margin: 0px 0px 0px 16px;
          }
          .promo__body {
            padding-top: 0;
          }
        }
        @include flex-basis(33.3333%);
        position: relative;
        display: flex;
        margin-bottom: $vertical-spacing;

        .promo {
          min-height: 220px;
        }
        .metadata {
          position: absolute;
          bottom: 0;
        }
      }
    }
    .sulfur {
      .sulfur__list-item {
        position: relative;
        margin-top: $list-item-top-margin;

        &:first-child {
          @include flex-basis(66.66666%);

          .see-also {
            li {
              display: inline-block;
              width: 50%;

              &:nth-child(-2n + 3) {
                padding-right: 16px;
              }
            }
          }
        }
        &:nth-child(2) {
          @include flex-basis(33.333333333%);

          &:before {
            content: '';
            display: block;
            position: absolute;
            top: 0px;
            bottom: auto;
            border-left: 1px solid $keyline;
            height: 100%;
            border-top: 0px;
          }
          .neon {
            .promo {
              padding-bottom: 32px;
            }
            .metadata {
              position: absolute;
              bottom: 0;
            }
          }
        }
      }
    }
    .silicon {
      width: 100%;
      float: left;

      .promo__body {
        padding-bottom: 8px;
      }
    }
    .antimony {
      width: 100%;

      .antimony__list-item {
        @include flex-basis(33.333333333%);
      }
    }
  }
  //END GROUP 3



  @include mq(g4) {

    .radon__list-item {
      + .radon__list-item {

        .promo:before {
          content: "";
          display: block;
          position: relative;
          top: -$vertical-spacing / 2;
          bottom: auto;
          left: 0;
          width: calc(100%);
          border-top: 1px solid $keyline;
        }
      }
      flex-basis: 100%;
    }
    .neon {
      display: flex;
    }
    .sulfur {
      width: 66.666666667%;
      float: left;

      .sulfur__list-item {
        &:first-child {
          @include flex-basis(66.666666667%);
        }
        &:nth-child(2) {
          @include flex-basis(33.333333333%);
        }
      }
    }
    .antimony {
      width: 100%;
      display: inline-flex;

      &:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        bottom: auto;
        left: $list-item-top-margin / 2;

        width:calc(100% - #{
          $list-item-top-margin
        }
        );
        background-color: $keyline;
        height: 1px;
        border-left: 0px;
      }
      .antimony__list-item {
        @include flex-basis(20%);

        &:first-child {
        }
        &.mpu-advert {
          display: inline-block;
        }
      }
    }
  }
  //END GROUP 4

  @include mq(g5) {
    .tin__content {
      display: flex;
    }
    .boron {
      width: 56.25%;
      float: left;

      .promo {
        min-height: 260px;
      }
      ////////KEY LINES
      .boron__list-item {
        &:last-child:before {
          content: none;
        }
      }
    }
    .sulfur {
      width: 75%;

      .sulfur__list-item {
        &:first-child {
          .see-also {
            li {
              display: inline-block;
              width: 33.333333333%;

              &:nth-child(3n + 2), &:nth-child(3n + 3) {
                padding-left: 16px;
              }
            }
          }
          .promo {
            margin-left: $g3-gutter-width / 2;
            margin-right: $g3-gutter-width / 2;
          }
          @include flex-basis(75%);
        }
        &:nth-child(2) {
          @include flex-basis(25%);
        }
      }
    }
    .silicon {
      width: 18.75%;
      float: left;
    }
    .antimony {
      margin-top: 0;
      padding: 0 8px;
      width: 25%;

      &:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        bottom: auto;
        left: 0;
        width: 1px;
        background-color: $keyline;

        height:calc(100%);
      }
      .antimony__list-item {
        .promo {
          margin: 0 4px;
        }
        &:first-child {
          @include flex-basis(100%);
        }
        @include flex-basis(50%);
      }
    }

  }
  //END GROUP 5

  @include mq($from:g1, $until:g3) {
    .boron {
      .boron__list-item {
        &:first-child {
          .metadata {
            margin-left: 0;
            left: 0;
          }
        }
        width: 100%;
        margin-top: $list-item-top-margin;

        &:before {
          content: '';
          display: block;
          position: relative;
          bottom: $list-item-top-margin / 2;
          left: $g4-gutter-width / 2;

          width:calc(100% -  #{
            $g4-gutter-width
          }
          );
          border-top: 1px solid $keyline;
        }
      }
    }
    .sulfur {
      .sulfur__list-item {
        width: 100%;

        &:first-child {
          margin-bottom: $list-item-top-margin;
        }
      }
    }
    .silicon {
      margin-top: $list-item-top-margin / 2;
    }
    .antimony {
      .mpu-advert {
        display: none !important;
      }
    }
  }
  //END GROUP 1 to GROUP 3
  @include mq($from:g3, $until:g4) {
    .arsenic {
      float: left;
      display: inherit;
      width: 50%;
      margin-top: $list-item-top-margin;

      .image {
        margin: 0 auto 0;
      }
    }
    .antimony {
      .mpu-advert {
        display: none !important;
      }
    }

    .silicon {
      margin-top: $list-item-top-margin;

      .radon__list:before {
        content: none;
      }
      .radon__list {
        display: flex;
        flex-flow: row wrap;

        .radon__list-item {
          flex-basis: 33.333%;

          &:nth-child(-n+3) {
            margin-top: 0;

            .promo:before {
              content: none;
            }
          }
        }
      }
    }
    .tin__list.boron:after {
      content: '';
      display: block;
      position: relative;
      bottom: 0;
      left: $g4-gutter-width / 2;

      width:calc(100% -  #{
        $g4-gutter-width
      }
      );
      border-top: 1px solid $keyline;
    }
    .tin__list {
      &:last-child:after {
        content: none;
      }
      &:after {
        content: none;
      }
    }
  }
  //END GROUP 3 ONLY


  @include mq($from:g4, $until:g5) {
    .boron {
      width: 75%;
      // 44.444444445% previous value
      float: left;

      .boron__list-item {
        &:first-child {
          @include flex-basis(100%);

          h3 {
            @extend %double-pica, %double-pica--bold;
            letter-spacing: -1px;
          }
          @include flex-basis(100%);
        }
        @include flex-basis(33.3333%);
        position: relative;
        display: flex;
      }
      ////////KEY LINES
      .boron__list-item {
        &:last-child:before {
          content: none;
        }
      }
    }
    .silicon {
      width: 25%;
      float: left;

      .radon__list:before {
        content: none;
      }
    }
  }
  //END GROUP 4 ONLY
}
