
.block__item{
    display: flex;
}
.nitrogen{
@include organism__container;
margin-top: $list-item-top-margin;
border-bottom: 0px solid $keyline;
  
@include mq(g1){
.neon{width: 100%;}    
.nitrogen__container{
display: table;
    .promo{
        position: relative;
    }
}
.nitrogen__list{
  @include flexbox;
  position: relative;
  @include flex-flow(row wrap);
}
 
.boron{            
.boron__list-item {  
          &:last-child{
              flex-basis: 100%;
       h3{font-size: 16px; line-height: 20px;}
     p{display: block;}
       @include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   left,
/*IMAGE WIDTH -------------------*/   142px,
/*IMAGE PADDING -----------------*/   0px 8px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   120%,
/*PROMO BODY PADDING ------------*/   0px 0px 32px 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
        
    } 
    }
    }
  
    
.arsenic{
   display: none;
  }
    
.antimony{
 h3{ @extend %brilliant; font-weight: 700;letter-spacing: 0;  }
           
.antimony__list-item{    
width: 100%;  
    }

  
    } 
    .sulfur{
        .sulfur__list-item{
                position: relative;
                display: flex;
            & > div {display: flex;}
            &:after{
                
            }
        }
    }    
}//END GROUP 1   
    
@include mq(g3){
.boron{ 
.boron__list-item {  
  @include flex-basis(33.333333333%);
  position: relative;
  display: flex;
  margin-top: $list-item-top-margin;   

  &:last-child{
              flex-basis: 66.6666%;
       h3{font-size: 18px; line-height: 22px; letter-spacing: -0.5px;}
     
       @include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   right,
/*IMAGE WIDTH -------------------*/   48%,
/*IMAGE PADDING -----------------*/   0px 0px 0px 16px,
/*IMAGE HEIGHT (default 56.25%)--*/   120%,
/*PROMO BODY PADDING ------------*/   8px 0px 32px 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
        
    } 
    
&:nth-child(-n + 3):after{
  content: '';
  display: block;
  position: absolute;
  bottom: -$list-item-top-margin / 2;
  left: $g4-gutter-width / 2 ;
  width: calc(100% - #{ $g4-gutter-width } );
  border-bottom: 1px solid $keyline;
}
    
.promo {
 min-height: 236px;

 }
    
.metadata {
 position: absolute;
 bottom: 0;
}
    
@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 0px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   8px 0px $list-item-top-margin 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
}    
    
////////KEY LINES
.boron__list-item + .boron__list-item:before{
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  left: 0;
  border-left: 1px solid $keyline;       
  } 
    
.boron__list-item:nth-child(4):before{ content: none;}
}

.sulfur{
@include sectionAfterDivider(
  /*PSEUDO CLASS (after/before) ----*/before,
  /*DIVIDER CONTENT ----------------*/ '',
  /*TOP POSITION -------------------*/ auto,
  /*BOTTOM POSITION ----------------*/ -$list-item-top-margin / 2,
  /*LEFT POSITION ------------------*/ -140px,
  /*DIVIDER WIDTH ------------------*/ calc(100% + 140px)
  );    
.sulfur__list-item{
  position: relative;
  
&:first-child{

 @include flex-basis(66.66666%);
 @include promo-settings(
          /*PSEUDO CLASS (after/before) ----*/before,
/*DIVIDER CONTENT ----------------*/ none,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
        /*IMAGE WIDTH -------------------*/   100%,
        /*IMAGE PADDING -----------------*/   0px 0px 0px 0,
        /*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
        /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
        /*METADATA PADDING --------------*/   0 0px,
        /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
        );
        .see-also{
          li{
            display: inline-block;
            width: 50%;

            &:nth-child(-2n + 3){
          
            padding-right: 16px;
            }
          }
         }



  }

&:nth-child(2){
  @include flex-basis(33.333333333%);
  &:before{
   content: '';
   display: block;
   position: absolute;
   top: 0px;
   bottom: auto;
   border-left: 1px solid $keyline;
   height: 100%;
   border-top: 0px;
  }   
.neon{
   
  .promo{padding-bottom: 32px;}     
  .metadata{position: absolute; bottom: 0;}      
  @include promo-settings(
    /*PSEUDO CLASS (after/before) ----*/ before,
    /*DIVIDER CONTENT ----------------*/  none,
    /*PROMO PADDING -----------------*/   0,
    /*IMAGE FLOAT:right, left, none  */   left,
    /*IMAGE WIDTH -------------------*/   100%,
    /*IMAGE PADDING -----------------*/   0px 0px 8px 0px,
    /*IMAGE HEIGHT (default 56.25%)--*/   120%,
    /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
    /*METADATA PADDING --------------*/   0 0px,
    /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);    
}
  
}}}

.silicon{
 margin-top: $list-item-top-margin / 2;
 padding-top: $list-item-top-margin / 2;  
 width: 50%;
 float: left;    
} 

.antimony{ 
width: 100%;
.antimony__list-item{
margin-top:  $list-item-top-margin;    
@include flex-basis( 33.333333333%);  
}
@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/  '',
/*PROMO PADDING -----------------*/   $list-item-top-margin 0 0 0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 0px 8px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   8px 0px 0 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   0px 0 0px 0px
);    
}   
   
    
}//END GROUP 3  
    
    
    
@include mq(g4){
.boron{
 width: 66.666666666%; // 44.444444445% previous value
 float: left;
    } 

.sulfur{
  width: 66.666666667%;
  float: left;
.sulfur__list-item{
&:first-child{
  @include flex-basis(66.666666667%);
  }
&:nth-child(2){
  @include flex-basis(33.333333333%);

.neon{
@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 0px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   120%,
/*PROMO BODY PADDING ------------*/   8px 0px 0 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
}
    }
  
  }
}

.silicon{
 width: 100%;
 float: left;
}
 

.antimony{
width: 33.333333333%;   
    
@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/  none,
/*PROMO PADDING -----------------*/   0 0 0 0,
/*IMAGE FLOAT:right, left, none  */   left,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 0px 8px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   0px 0px 8px 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   0px 0 0px 0px
);
 
  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: auto;
    left: 0;
    border-left: 1px solid #dbdbdb;
    height: 100%;
    border-top: 0px;
}
   
 .antimony__list-item{
@include flex-basis(100%); 
    &:first-child {
    margin-top: 0;
    }
    &.mpu-advert{
    display: inline-block;
     }
}

  }    
}//END GROUP 4
    
@include mq(g5){
.boron{
 width: 56.25%;
 .promo{ min-height: 210px;}
}

.sulfur{
width: 75%;
@include sectionAfterDivider(
  /*PSEUDO CLASS (after/before) ----*/before,
  /*DIVIDER CONTENT ----------------*/ '',
  /*TOP POSITION -------------------*/ auto,
  /*BOTTOM POSITION ----------------*/ -$list-item-top-margin / 2,
  /*LEFT POSITION ------------------*/ -500px,
  /*DIVIDER WIDTH ------------------*/ calc(100% + 500px)
  );
      
.sulfur__list-item{
&:first-child{
     .see-also{
          li{
            display: inline-block;
            width: 33.333333333%;

            &:nth-child(3n + 2), &:nth-child(3n + 3){
            padding-left: 16px;
            }
          }
         }
.promo{
margin-top:0px;
margin-left: $g5-gutter-width / 2;
margin-right: $g5-gutter-width / 2;
  }
@include flex-basis(75%);

@include promo-settings(
        /*PSEUDO CLASS (after/before) ----*/before,
        /*DIVIDER CONTENT ----------------*/ none,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   right,
        /*IMAGE WIDTH -------------------*/    65.1%,
        /*IMAGE PADDING -----------------*/   0px 0px 0px 8px,
        /*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
        /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
        /*METADATA PADDING --------------*/   0 0px,
        /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
        );

  }

 &:nth-child(2){

@include flex-basis(25%);
.neon{
@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 0px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   8px 0px 0 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
}
    }
}
}

.silicon{
  width: 18.75%;
  float: left;
  }  
    
.antimony{
      width: 25%;
  }    
}//END GROUP 5 
    
@include mq($from: g1, $until:g3){
.boron{
    .boron__list-item { 
    width: 100%;
    margin-top: $list-item-top-margin; 
    &:before{
    content: '';
    display: block;
    position: relative;
    bottom: $list-item-top-margin / 2;
    left: $g4-gutter-width / 2 ;
    width: calc(100% -  #{$g4-gutter-width} );
    border-top: 1px solid $keyline;
    }    
  }       
} 
    
.sulfur{
.sulfur__list-item{
 width: 100%;    
 &:first-child{
     
  &:after{
  content: '';
  display: block;
  position: absolute;
  bottom: -$list-item-top-margin / 2;
  left: -$g2-gutter-width / 2;
  width: calc(100% + #{ $g2-gutter-width });
  border-bottom: 1px solid $keyline; 
 }       
}
     &:nth-child(2){
         margin-top: $list-item-top-margin;
        
}
    }
}  

.silicon{
        margin-top: $list-item-top-margin / 2;

}  
 
 .antimony{   
   .mpu-advert{display: none !important;}
    }
     
}//END GROUP 1 to GROUP 3  
@include mq($from: g3, $until:g4){
.arsenic{
  float: left;
  display: inherit;
  width: 50%;
  .arsenic__list-item{    
  margin-top: $list-item-top-margin;
    }
  .image{margin: 0 auto 0;}    
}    
 .antimony{   
   .mpu-advert{display: none !important;}
    }       
}//END GROUP 3 ONLY 

}






