.lead__content {
  display: inline-block;
  flex-flow: row wrap;
  margin-bottom: -$vertical-spacing;
}

.lead{
    @include organism__container;
}
.lead {
  margin-bottom: $slice-vertical-spacing;
  overflow: hidden;

.radon__headlines{
  width: 100%;
  @include mq(g3) {
  .radon__list-item .promo{
    min-height: 70px;
    padding-top:8px;
  }
}
}
  .lead__promo {
    .promo__body h3 {padding-top:16px;}
  }

    .lead__list-item, .radon__list-item {

      @include mq($from:g1, $until:g3) {
  margin-bottom: $g3-gutter-width;
    }
    @include mq($from:g3, $until:g4) {
  margin-bottom: $g3-gutter-width;
    }
    @include mq($from:g4, $until:g5) {
  margin-bottom: $g4-gutter-width;
    }
    @include mq(g5) {
  margin-bottom: $g5-gutter-width;
    }
  }


  @include mq(g3) {
    .lead__list {
      display: flex;
      flex-flow: row wrap;
    }
    .metadata {
        position: absolute;
        bottom: 0;
      }
    .promo {

      .promo__body {
        padding-bottom: 24px;
      }
      .promo__image-wrapper {
        float: none;
        width: 100%;
      }
      .promo__image-wrapper {
        margin-bottom: 8px;
      }
    }
    .third__column {
      .radon.radon__headlines {
        h3 {
          @extend %english;
        }
      }
    }
  }

  @include mq(g4) {
    .lead__promo .promo .promo__image-wrapper {
      width: 65.5%;
      float: right;
      margin: 0 0 0 12px;
    }
  }

  @include mq(g5) {
    .lead__promo.lead__list-item {
      display: flex;
      flex-basis: 80%;
    }
    .lead__promo .promo .promo__image-wrapper {
      width: 50%;
      float: right;
      margin: 0 0 0 12px;
    }
    .slice {
      width: 100%;
      display: inline-block;
      float: left;

      .lead__list-item {
        width: 20%;
      }
    }
    .first__column {
      vertical-align: top;
      width: 80%;
      display: inline-block;
      float: left;

      .lead__list-item {
        display: flex;
        flex-basis: 25%;
      }
      .lead__promo {
        &.lead__list-item {
          display: flex;
          flex-basis: 75%;
        }
        .promo {
          .promo__image-wrapper {
            float: right;
            width: 66.66%;
          }
          .promo__body {
            h3 {
              margin-top: 8px;
            }
          }
        }
      }
    }
    .second__column {
      width: 40%;
      display: inline-block;
      vertical-align: top;
      margin-top: 16px;
      float: left;

      .lead__list-item {
        width: 50%;
        display: inline-block;
      }
      .promo {
        margin-top: 8px;
      }
    }

    .third__column {
      display: inline-block;
      width: 20%;
    }

    .lead__list {
      display: flex;
      flex-flow: row wrap;
    }
    .promo .promo__image-wrapper {
      float: none;
      width: 100%;
    }
  }

  @include mq($from:g1, $until:g3) {
    .third__column {
      width: 100%;
    }
    .radon__headlines {
      margin-top: 24px;

      .radon__list .radon__list-item {
        &:first-child {
          .promo {
            margin-top: 16px;
          }
        }
      }
      .promo {
        margin-top: 0px;
      }
    }

    .lead__promo {
      &.lead__list-item {
        display: flex;
        flex-basis: 100%;
      }
      .promo {
        margin-top: 0;

        .promo__body {
          padding-bottom: 16px;

          h3 {
            margin-top: 8px;
          }
        }
        .metadata {
          left: 0;
          margin: 0;
          position: relative;
        }
      }
    }
  }
  @include mq($from:g3, $until:g4) {
    .lead__list {
      display: flex;
      flex-flow: row wrap;
    }
    .lead__list-item {
      display: flex;
      flex-basis: 33.333%;
    }
    .promo {
      .promo__image-wrapper {
        float: none;
        width: 100%;
      }
    }
    .slice {
      width: 100%;
      display: inline-block;
      float: left;

      .lead__list {
        .lead__list-item {
          flex-basis: 33.333%;

          &.lead__promo {
            flex-basis: 66.666%;

            .promo {
              .promo__image-wrapper {
                float: none;
              }
            }
          }
        }
      }
    }
    .first__column, .second__column {
      display: flex;
      width: 66.66%;
      float: left;

      .lead__list-item {
        flex-basis: 50%;
        width: 50%;
        display: flex;
      }
    }
    .third__column {
      width: 33.333%;
      display: inline-block;

      .lead__list-item {
        flex-basis: 100%;
      }
    }
  }
  @include mq($from:g4, $until:g5) {
    .slice {
      width: 100%;
    }
    .lead__promo {
      &.lead__list-item {
        display: flex;
        flex-basis: 75%;
      }
    }

    .first__column {
      vertical-align: top;
      width: 100%;
      display: inline-block;
      float: left;

      .lead__list-item {
        display: flex;
        flex-basis: 25%;
      }
    }
    .third__column {
      width: 100%;
      display: inline-block;
      position: relative;


      .radon__list:before {
        content: none;
        display: block;
        height: 1px;
        width: calc(100% - 24px);
        background-color: $keyline;
        position: absolute;
        top: -$vertical-spacing;
        border-top: 0;
        left: $g5-gutter-width / 2;
      }
      .lead__list-item {
        flex-basis: 100%;
      }
      .radon__list {display: flex; flex-flow: row wrap;}
      .radon__list .radon__list-item {
        flex-basis: 25%;

        .promo:before {
          content: none;
        }
      }
    }
    .lead__list-item {
      display: flex;
      flex-basis: 25%;
    }
  }
}
