


.fluorine {
      @include organism__container;
  @include quad__organism;
    background: #FFF;
//    @include quad__organism;

    .promo {
            h3 {
                @extend %paragon;
            }
        }

    .fluorine__primary-col {
        overflow: hidden;
        @include grid__item;
        @include mq(g4) {
            width: calc(4.166666667% * 16);
        }
        @include mq(g5) {
            width: calc(4.166666667% * 18);
        }
    }
    .fluorine__primary-col__group {
        @include grid__item;
        display: flex;
        flex-wrap: wrap;
        align-content: stretch;
        align-items: stretch;
        float: left;
        @include mq(g4) {
            width: calc(4.166666667% * 24);
        }
        @include mq(g5) {
            width: calc(4.166666667% * 24);
        }
      .fluorine__item {
        .promo{
          .promo__content{
            padding-bottom: 32px;
          }
          .metadata{
            position: absolute;
            bottom: 0;
          }
        }
        position: relative;
        display: flex;
        @include gutter($gutter-width);
        @include organism__item;
        @include organism_first__item (
               /*G3 promo cols*/     16,
               /*G3 image position*/ none,
               /*G3 image size*/     1,
               /*G3 image padding*/  0,
               /*G5 image size*/     1.5,
               /*G5 promo cols*/     18,
               /*G5 image position*/ right,
               /*G5 image padding*/  0
               );
        &.first__item{
          .promo__body{
            padding-bottom: 24px;
          }
        .promo{
          .promo__content{
            padding-bottom: 0px;
          }
        }
        }
      @include mq(g3) { width: calc(4.166666667% * 8);}
      @include mq(g5) { width: calc(4.166666667% * 6);}



        }
    }
    .fluorine__third-col {
        @include grid__item;
        @include mq(g4) {
            width: calc(4.166666667% * 8);
        }
        @include mq(g5) {
            width: calc(4.166666667% * 6);
        }
    }
    &.fluorine__container {
        background-color: #fff;
        display: flex;
    }
    .fluorine__psecondary-col__group {
        @include grid__item;
    }

    //Hydrogen styles
    .hydrogen {
        position: relative;
        @include gutter($gutter-width);
        width: calc(4.166666667% * 6);
        @include mq(g3) {
            .hydrogen__list {
                flex-wrap: wrap;
                display: flex;
                width: 100%;
            }
            .hydrogen__list--item {
                &:nth-child(2n+1) {
                    clear: both;
                }
              &:last-child{flex-basis: calc(100% / 3);}
                flex-basis: calc(100% / 3);
                margin-bottom: 0;
                padding-bottom: 0;
            }
        }
        @include mq(g5) {
            margin-top: 12px;
            .hydrogen__list--item {
         &:last-child{flex-basis: calc(100% / 1);}
                flex-basis: calc(100% / 1);
            }
        }
        .promo {
            h3 {
                @extend %english;
            }
        }
        .hydrogen__item {
            @include mq(g3) {
                 margin-top: $H-padding-top;
                  padding-top: $H-padding-top;
                &:first-child {

                    &:before {
                        content: '';
                    }
                }
            }
            @include mq(g5) {
                &:first-child {
                    padding-top: 0;
                    &:before {
                        content: '';
                    }
                }
            }
        }
    }
}
