// _organisms.scss

// Navigation

.orb{
height: 40px;
background-color: #fff;
display: block;
width: 100%;
}


.navigation{
width: 100%;
display: block;
height: 92px;
background-color: #A91717;

    .navigation-banner{
        height: 56px;
        width: 100%;
        display: block;
        background-color: $news;
    }

    @include mq($from:g1, $until:g3){
        height: 56px;
        background-color: $news;
        .navigation-banner{
        height: 32px;
        }
    }
}

// Organisms container


@mixin grid__item {
    display: inline-block;
    box-sizing: border-box;
    vertical-align: top;
}



@mixin gutterHorizontal($gutter-width, $border-size)  {
  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: -$gutter-width / 2;
    right: 0;
    width: 100%;
    height: $border-size;
    border-bottom: 0.0625rem solid $keyline;
  }
    &:first-child:before{border-left: 0;}

  margin-bottom: $gutter-width;
}

@mixin gutter($gutter-width)  {
  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 0.0625rem;
    height: 100%;
    border-left: 0.0625rem solid $keyline;
  }



}
// @include horizontal__promo(right, 1.5, 8px);
// displays horizontal or vertical promo with image left or right or vertical
// it also controls the badge position
@mixin horizontal__promo(
  $image-position,
  $image-width,
  $image-padding
  ) {
@if $image-position == 'right' {

      .image{
        float: right;
        width: calc(100% / #{$image-width});
        @include mq(g1) {
          padding-top: $image-padding;
          padding-right: $image-padding;
          padding-left: 8px;
          padding-bottom:$image-padding;

        }
        @include mq(g3) {
          padding-top: $image-padding;
          padding-right: $image-padding;
          padding-left: 12px;
          padding-bottom:$image-padding;
        }
      }


    .promo__label {
    position: relative;
    display: table;
    margin-bottom: 4px;
    top: 0;
    left: 0;
    z-index: 10;
    }

    .see-also{
    clear: both;
        li{
            &:first-child {padding-left: 0;}
           @include mq(g1) {
         width: calc(100% / 1);
            }
            @include mq(g3) {
         width: calc(100% / 3);
            }
         display: inline-block;
         padding-left: 12px;
        }
    }


   } @else if $image-position == 'left'{

      .image{
        float: left;
        width: calc(100% / #{$image-width});
         @include mq(g1) {
          padding-top: $image-padding;
          padding-right: 8px;
          padding-left: $image-padding;
          padding-bottom:$image-padding;
           width: 150px;
        }
        @include mq(g3) {
          padding-top: $image-padding;
          padding-right: 12px;
          padding-left: $image-padding;
          padding-bottom:$image-padding;
        }
        @include mq(400px) { width: 180px;}
        @include mq(600px) { width: calc(100% / #{$image-width});}
      }

     .promo__body{
       @include mq(g1) {
          padding-top: $image-padding - 2;
       }
        @include mq(g3) {
          padding-top: $image-padding + 2;
       }
      }
    .promo__label {
    position: relative;
    display: table;
    margin-bottom: 4px;
    }
    .see-also{
    clear: both;
        li{
            &:first-child {padding-left: 0;}
        @include mq(g1) {
         width: calc(100% / 1);
            }
            @include mq(g3) {
         width: calc(100% / 3);
            }
         display: inline-block;
         padding-left: 8px;
        }
    }

   }
     @else {

      .image{
        float: none;
        width: calc(100% / #{$image-width});
        padding: 0;
      }

    .promo__label {
    position: absolute;
    display: block;
    margin-bottom: 4px;
    }
    .see-also{
    clear: both;
        li{
            &:first-child {padding-left: 0;}
        @include mq(g1) {
         width: calc(100% / 1);
            }
            @include mq(g3) {
         width: calc(100% / 3);
            }
         display: inline-block;
         padding-left: 0px;
        }
    }

}
}



