@include mq(g3){
.boron{
.boron__list-item {
  @include flex-basis(33.333%);
  position: relative;
  display: flex;
  margin-bottom: $vertical-spacing;


&:nth-child(-n + 4):after{
  content: none;
  display: block;
  position: absolute;
  bottom: -$list-item-top-margin / 2;
  left: $g4-gutter-width / 2 ;
  width: calc(100% - #{ $g4-gutter-width } );
  border-bottom: 1px solid $keyline;
}

.promo {
 min-height: 216px;

 }

.metadata {
 position: absolute;
 bottom: 0;
}

@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/ none,
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 0px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   8px 0px $list-item-top-margin 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
}

////////KEY LINES
.boron__list-item + .boron__list-item:before{
  content: none;
  display: block;
  height: 100%;
  position: absolute;
  left: 0;
  border-left: 1px solid $keyline;
  }

.boron__list-item:nth-child(4):before{ content: none;}
}

.sulfur{
@include sectionAfterDivider(
  /*PSEUDO CLASS (after/before) ----*/before,
  /*DIVIDER CONTENT ----------------*/ '',
  /*TOP POSITION -------------------*/ auto,
  /*BOTTOM POSITION ----------------*/ -$list-item-top-margin / 2,
  /*LEFT POSITION ------------------*/ -140px,
  /*DIVIDER WIDTH ------------------*/ calc(100% + 140px)
  );
.sulfur__list-item{
  position: relative;



&:nth-child(2){
  @include flex-basis(33.333333333%);
  &:before{
   content: '';
   display: block;
   position: absolute;
   top: 0px;
   bottom: auto;
   border-left: 1px solid $keyline;
   height: 100%;
   border-top: 0px;
  }
.neon{

  .promo{padding-bottom: 32px;}

  @include promo-settings(
    /*PSEUDO CLASS (after/before) ----*/ before,
    /*DIVIDER CONTENT ----------------*/  none,
    /*PROMO PADDING -----------------*/   0,
    /*IMAGE FLOAT:right, left, none  */   left,
    /*IMAGE WIDTH -------------------*/   100%,
    /*IMAGE PADDING -----------------*/   0px 0px 8px 0px,
    /*IMAGE HEIGHT (default 56.25%)--*/   120%,
    /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
    /*METADATA PADDING --------------*/   0 0px,
    /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
}

}}}

.silicon{
 margin-top: $list-item-top-margin / 2;
 padding-top: $list-item-top-margin / 2;
