


.oxygen {
    @include quad__organism;
    @include organism__container;
.promo {

            h3 {
                @extend %paragon;
            }
        }
    .oxygen__primary-col {
        overflow: hidden;
        @include grid__item;
        @include mq(g4) {
            width: calc(4.166666667% * 16);
        }
        @include mq(g5) {
            width: calc(4.166666667% * 18);
        }
    }
    .oxygen__primary-col__group {
        @include grid__item;
        display: flex;
        flex-wrap: wrap;
        align-content: stretch;
        align-items: stretch;
        float: left;
        @include mq(g4) {
            width: calc(4.166666667% * 24);
        }
        @include mq(g5) {
            width: calc(4.166666667% * 18);
        }
    }
    .oxygen__secondary-col-group__inner-col {
        position: relative;
        clear: both;
        @include mq(g2) {
            width: calc(4.166666667% * 24);
            margin-top: 0px;
            display: inline-block;
        }
        @include mq(g5) {
            @include gutter($gutter-width);
            @include grid__item;
            margin-top: 0;
            width: calc(4.166666667% * 6);
            margin-left: 0;
            .hydrogen {
                display: block;
                flex-wrap: nowrap;
                .hydrogen__item {
                    flex-basis: 100%;
                }
            }
        }
    }
    .oxygen__third-col {
        @include grid__item;
        @include mq(g4) {
            width: calc(4.166666667% * 8);
        }
        @include mq(g5) {
            width: calc(4.166666667% * 6);
        }
    }
    &.oxygen__container {
        background-color: #fff;
        display: flex;
    }
    .oxygen__psecondary-col__group {
        @include grid__item;
    }
    .oxygen__item {
      display: flex;
      &.first__item{
        .promo{
        .promo__content{
          padding-bottom: 0px;
        }

      }
      }
      .promo{
        padding-bottom:32px;
        .metadata{
      position: absolute;
      bottom: 0;
          }
        .promo__content{

          width: 100%;
        }
      }
         @include organism__item;
         @include organism_first__item (
           /*G3 promo cols*/     24,
           /*G3 image position*/ right,
           /*G3 image size*/     1.5,
           /*G3 image paddign*/  0,
           /*G5 image size*/     1.5,
           /*G5 promo cols*/     24,
           /*G5 image position*/ right,
           /*G5 image paddign*/  0
           );

          @include mq(g2) {
              width: calc(100% / 1);

             @include horizontal__promo(left, 2.5, 0px);


        }


        @include mq(g3){



        width: calc(100% / 3);

        @include horizontal__promo(none, 1, 0);

         @include gutter($gutter-width);



        }
    }
    //Hydrogen styles
    .hydrogen {
        display: inline-block;
          @include mq(g1) {
          margin-top: 16px;
              .hydrogen__item:first-child{padding-top: $H-padding-top; }
            .hydrogen__list {
                flex-wrap: wrap;
                display: flex;
                width: 100%;
            }
            .hydrogen__list--item {
                &:nth-child(2n+1) {
                    clear: both;
                }
                flex-basis: calc(100% / 1);
                margin-bottom: 0;
                padding-bottom: 0.75rem;
            }
        }
        @include mq(g3) {
            margin-top: $H-padding-top;
            padding-top: $H-padding-top;
            .hydrogen__list {
                flex-wrap: wrap;
                display: flex;
                width: 100%;
            }
            .hydrogen__list--item {
                &:nth-child(2n+1) {
                    clear: both;
                }
                flex-basis: calc(100% / 3);
                margin-bottom: 0;
                padding-bottom: 0.75rem;
            }
        }
        .promo {
  .promo__container{
    .metadata{padding: 0;}
  }
            h3 {
                @extend %english;
            }
        }
        .hydrogen__item {
            @include mq(g3) {
                &:first-child {
                    padding-top: $H-padding-top;
                    &:before {
                        content: '';
                    }
                }
            }

             @include mq(g5) {
                &:first-child {
                    padding-top: 0;
                    &:before {
                        content: '';
                    }
                }
            }
        }
    }
}
