.carousel{
	margin-top: 24px;
}
.carousel__item{
	display: flex;

	.promo__image-wrapper{
		position: relative;
	}
	.responsive-image img {
    height: 100%;
    position: absolute;
    width: 100%;
        display: block;
}
.responsive-image--16by9 {
    height: 0;
    overflow: hidden;
    padding-bottom: 56.25%;
}
img {
    height: auto;
    -ms-interpolation-mode: bicubic;
    max-width: 100%;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    width: auto;
}
.responsive-image {
    background-color: #f0f0f0;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' 534-9.746-3.534H14.964zM0 .016h57.044V53.7H0V.016z'/%3E%3C/g%3E%3C/svg%3E");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 30%;
    height: 0;
    overflow: hidden;
    position: relative;
}

.english{@extend %english; margin-top: 8px; padding-bottom: 16px;}

 .english, .metadata, .metadata svg,.metadata a,.metadata__time {color: #444; fill: #444;}
	.small__image-promo, .small__image-promo .top-story__cta{background-color: #ECECEC;}
	.hp-tv-radio-asset__cta-duration{color: #444;}
	.brilliant{
		color: #444;letter-spacing:0;
		font-weight: 700;
		margin-top: 8px;	
		margin-bottom: 8px;
	}
	}

