$navigation-keyline: #D03838;
.nav-top.news {
  @include mq(g3) {
    margin-bottom:16px;
  }

  .global-header__wrap, .primary-nav__items-wrap {
    @include mq($from:g4, $until:g5) {
      width: 1008px;
    }
  }
  .global-header {
    background-color: $news;
  }
  .primary-nav {
    background-color: $news;
    border-top: 1px solid $navigation-keyline;
  }
  .primary-nav__link-text {
    border-left: 1px solid $navigation-keyline;
  }
  .primary-nav__item--pinned {
    border-left: 1px solid $navigation-keyline;
  }
  .primary-nav__link {
    color: #fff;
  }
  .primary-nav__link:active, .primary-nav__link:hover{
    border-bottom: 4px solid #fff;
  }
  .global-header__logo {
    color: #fff;
    -webkit-font-smoothing: antialiased;
  }
  .ico-burger {
    background-position: -74px -24px;
  }
  .global-header__all-sports-button {
    background-color: $news;
    border: 1px solid $navigation-keyline;
    color: #fff;
}
}
