// _atoms.scss
//

// LAYOUT
// - Grid

@import 'grid';


// COLOUR
// - Reds

$news:     #BB1919;
$newsbeat: #4D2C7A;
$sport:    #ffdf43;

// - Greys

$body-copy:    #000;
$summary-copy: #444444;
$keyline:      #DCDCDC;

// - Blues

$link:       #222222;
$link-hover: #1167A8;
$featured:   #2674A1;

$featuredColour: #00FFBB;

$standout--promo-h1:#fff;
$standout--promo-bgcolor:#000;
$standout--promo-gradient:#000;
$standout--promo-fgcolour:#fff;


// TYPOGRAPHY
// - Fonts

@mixin bold   { font-weight: bold;
    -webkit-font-smoothing: antialiased; }
@mixin italic { font-style: italic; }

@mixin ff-arial   { font-family: Helmet, Freesans, Helvetica, Arial, sans-serif; }
@mixin ff-georgia { font-family: Georgia, serif; }

// - Scale

@import 'typography-settings';

@mixin output-typographic-scale($settings) {
  @each $type-class, $class-groups in $settings {
    %#{$type-class} {
      @each $group, $type-properties in $class-groups {
        @include output-type-group-styles($group, $type-properties);
      }
    }
    %#{$type-class}--bold {
      @include bold;
      letter-spacing: -0.5px;
    }
  }
}

@mixin output-type-group-styles($group, $type-properties) {
  @include mq($group) {
    @each $type-property, $type-value in $type-properties {
      #{$type-property}: $type-value;
    }
  }
}

@include output-typographic-scale($typographic-scale-settings);

// - STRAPLINE
.strapline{
  .strapline__title{
     @extend %great-primer;
     margin-bottom: 0;
     position: relative;
     }

  @include mq(g2){
   margin:  0 $g2-gutter-width / 2  16px;
   padding-top: 16px;
    }

  @include mq(g3){
    margin:  0 $g3-gutter-width / 2  16px;
    padding-top: 16px;
    }

  @include mq(g4){
    margin:  0 $g4-gutter-width / 2  16px;
    padding-top: 16px;
    }

  @include mq(g5){
    margin-bottom: 16px;
    padding-top: 16px;
    margin-top: 0;
    margin-left: $g5-gutter-width / 2;
    margin-right: $g5-gutter-width / 2;
    }

  @include mq(g1) {
  &:before{
    content: '';
    display: block;
    position: relative;
    top: -16px;
    left: 0;
    width: calc(100%);
    background-color: $keyline;
    height: 1px;
       }
  }

}

// MICROCOPY
// - Label

.featured--label{
    background-color: $news;
    display: table;
    padding: 0.25em 0.45em 0.15em;
    font-weight: 700;
    color: #fff;
    margin-bottom: 4px;
  }

.label {
  @extend %minion;
  background-color: #353535;
  color: #FFF;
  display: inline-block;
  line-height: 1;
  padding: 0.25em 0.45em 0.15em;
  vertical-align: middle;
  -webkit-font-smoothing: antialiased;

  &.breaking{
    background-color: $news;
    display: table;
  }
   &.sport{
    color: #000;
    background-color: $sport;
     top: -3px;
     position: relative;

  }
     &.featured2{
    color: #000;
        display: table;
    background-color: $featuredColour;
     top: 6px !important;
     position: relative;

  }
  &.newsbeat{
    background-color: $newsbeat;
     top: -3px;
     position: relative;
  }
   &.live-news{
    color: #fff;
    background-color: $news;
     top: -1px;
     position: relative;
  }
  &.live-news-image{
    position: absolute;
     @include mq($from:g1, $until:g3) {
      position: relative;
    }
  }
}
.label--inline {
  margin-top: -0.25em;
}
.label--icon {
  padding-right: 0.5em;
  padding-left: 0.5em;
}


// LISTS
// - Nav

@mixin nav {
  list-style: none;
  padding-left: 0;

  > li {

    &,
    > a {
      display: inline-block;
    }
  }
}

@mixin nav--stacked {

  > li {
    display: list-item;

    > a {
      display: block;
    }
  }
}


// MEDIA
// - Image

.image {
  max-width: 100%;
}

// - Avatar

.avatar {
  background-color: #EEE;
  border-radius: 50%;
  height: auto;
  width: 44px;
}


// ICONOGRAPHY
// - Branch

@mixin branch {

  &::before {
    background-color: #f1f1f1;
    content: '';
    display: block;
    margin-bottom: 8px; // + 1 = 4
    height: 1px;
    width: 40px;
  }
}


// - Icons

.icon {
  display: inline-block;

  &,
  svg {
    height: 32px;
    width: 32px;
  }
}
  .icon--type {
    line-height: 1;
    margin-top: -0.25em;
    vertical-align: middle;

    &,
    svg {
      height: 1em;
      width: 1em;
    }
  }
  .icon--label {

    &,
    svg {
      height: 1em;
      width: 1em;
    }
    svg {
      fill: #FFF;
    }
  }




.gel-icon svg {
    height: inherit;
    fill: currentColor;
}




.gs-o-live-status {
    color: $news;
   font-weight: bold;
}
.gs-o-live-status {
    display: inline-block;

}

.gs-o-live-status__icon {
    width: .99em;
    height: .99em;

    margin-right: 0px;
}


@-webkit-keyframes pulse {
    0% {
        opacity: 1
    }

    50% {
        opacity: .3
    }

    100% {
        opacity: 1
    }
}

@keyframes pulse {
    0% {
        opacity: 1
    }

    50% {
        opacity: .3
    }

    100% {
        opacity: 1
    }
}

.gs-o-live-status__icon #pulse {
    -webkit-animation: pulse 1.7s cubic-bezier(0.25, 0.1, 0.25, 1) infinite;
    animation: pulse 1.7s cubic-bezier(0.25, 0.1, 0.25, 1) infinite;
}


