a{
  &.topic-tag{
    margin-bottom: 10px;
    font-weight: 500;
    letter-spacing: 2px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 10px;

  }

   &.topic-tag {
    color: #000;
    display: inline-block;
    cursor: pointer;
    padding: 0 12px;
    height: 28px;
    border: 1px solid #000;
    line-height: 28px;
    letter-spacing: 2px;
    text-transform: uppercase;
    max-width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
     &:hover{color: #fff;background: #000;}
    }

}

