// _molecules
//
//Video Promo

.video__promo{
    padding-top: 12px;
    .promo{border-bottom: 1px solid $keyline; padding-bottom: 12px;margin-bottom: 12px;


    }
    h3 {
     @extend %english;
    }
}

// Cards Hype
#cards_hype_container, #mostpopular_hype_container{

  @include mq(g2) {height: 683px !important;}
  @include mq(g4) {height: 712px !important;}
   @include mq(g5) {height: 583px !important;}
    width: 300px !important;
  margin-top: 0px !important;
}

.temp_chart{
    bottom: 260px;
}
// Content
// - Contributor



.contributor {
  display: table;
  width: 100%;
 margin-top: 6px;
padding-bottom: 16px;
 @include mq($from:g1, $until:400px) {
  padding-bottom:0;
 }
 @include mq($from:375px, $until:g3) {
display: block;
padding-bottom: 8px;
}
  .contributor__avatar {
    padding-right: 12px;

    > img {
      display: block;
      max-width: none;
    }
  }
  .contributor__body {
    width: 100%;
  }

  .contributor__avatar,
  .contributor__body {
    display: table-cell;
    vertical-align: middle;
  }
    .contributor__name {
      font-family: georgia;
      font-style: italic;
      @extend %brilliant;

      color: $link;
    }
    .contributor__title {
      font-size: 13px;
      line-height: 18px;
      color: $summary-copy;
      -webkit-font-smoothing: antialiased;
    }

    .contributor__name,
    .contributor__title {
      display: block;
      font-weight: 400;
       @include mq($from:g1, $until:g3) {
        display:inline-block;
        }
}
    }

// - Metadata

.metadata {
  @include nav;

  margin-bottom: 0;

  li {
    @extend %brevier;
    border-right: 1px solid $keyline;
    margin-right: 6px;
    padding-right: 6px;

    &:last-child {
      border-right: none;
      margin-right: 0;
      padding-right: 0;
    }
  }
  .faux-block-link a { z-index: 999;}
  .icon {
    margin-right: 4px;
  }
}
  .metadata__time {
    color: $summary-copy;

    .icon svg {
      fill: $summary-copy;
    }
  }
  .metadata__section {position:relative;}
  .metadata__comments {}

.see-also {
  @include nav;
  @include nav--stacked;
  @include branch;
  margin-top: 12px;
  margin-bottom: 0;

  li {
    vertical-align: top;
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    @extend %brilliant;
    padding-top: 4px;
    padding-bottom: 4px;
  }
  .label--icon {
    padding-right: 0.3em;
    padding-left: 0.3em;
    padding-top: 0.1em;
    padding-bottom: 0em;
    margin-right: 4px;
      .icon--label {

    &,
    svg {
      height: 0.8em;
      width: 0.6em;
    }
    svg {
      fill: #FFF;
    }
  }
  }
}

// ADVERTS
.leaderboard {
  @include mq(g1) {display: none;}
  @include mq(728px) {
    margin: 16px auto 0 auto;
    width: 728px;
    display: inherit;
  }
}
.advertisement-label {
    float: right;
    font-size: 10px;
    color: #404040;
    padding-bottom: 2px;
}


// Hydrogen Molecule



//Hydrogen variables
$H-padding-top:    12px;
$H-padding-bottom: 12px;
.hydrogen{

    flex: 2 1 auto;
    display: flex;
    .promo__container{

    @include horizontal__keyline;
    }

    .list--columns{
        flex-wrap: wrap;
        display: flex;
    }

ul.unstyled{
  padding-left: 0;
  list-style: none;
  margin: 0;
}

  h3 {
    @extend %paragon, %great-primer--bold;
    margin-top: 0;
    margin-bottom: 0;
  }

  .hydrogen__item{
    &:first-child{
      padding-top: 0;
      &:before{content: none;}
    }


    position: relative;
    padding-top: $H-padding-top;
    padding-bottom: $H-padding-bottom;

    }

}
