.iron{
display: flex;
    .MPU{height: 250px; width: 300px; display: block; background-color: gray;margin: 0 auto; margin-top: 24px;}
    
@include organism__container;

.iron__container{
display: block;
    .promo{
        position: relative;
    }
}
    
@include mq(g1){
 .neon{width: 100%;}      
.boron{            
.boron__list-item {  
  
  

 }
}
    

.iron__list{
  @include flexbox;
  position: relative;
  @include flex-flow(row wrap);
}
    
.arsenic{
   display: none;
  }
    
.antimony{
 h3{ @extend %brilliant; font-weight: 700;letter-spacing: 0;  }
           
