width: 100%;  
    }
      
    }    
}//END GROUP 1   
    
@include mq(g3){

.sulfur{
@include sectionAfterDivider(
  /*PSEUDO CLASS (after/before) ----*/before,
  /*DIVIDER CONTENT ----------------*/ '',
  /*TOP POSITION -------------------*/ auto,
  /*BOTTOM POSITION ----------------*/ -$list-item-top-margin / 2,
  /*LEFT POSITION ------------------*/ -140px,
  /*DIVIDER WIDTH ------------------*/ calc(100% + 140px)
  );    
.sulfur__list-item{
  position: relative;
  margin-top: $list-item-top-margin;    
&:first-child{

 @include flex-basis(66.66666%);
 @include promo-settings(
          /*PSEUDO CLASS (after/before) ----*/before,
/*DIVIDER CONTENT ----------------*/ none,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
        /*IMAGE WIDTH -------------------*/   100%,
        /*IMAGE PADDING -----------------*/   0px 0px 0px 0,
        /*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
        /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
        /*METADATA PADDING --------------*/   0 0px,
        /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
        );
        .see-also{
          li{
            display: inline-block;
            width: 50%;

            &:nth-child(-2n + 3){
          
            padding-right: 16px;
            }
          }
         }



  }

&:nth-child(2){
  @include flex-basis(33.333333333%);
  &:before{
   content: '';
   display: block;
   position: absolute;
   top: 0px;
   bottom: auto;
   border-left: 1px solid $keyline;
   height: 100%;
   border-top: 0px;
  }   
.neon{
  .promo{padding-bottom: 32px;}     
  .metadata{position: absolute; bottom: 0;}      
  @include promo-settings(
    /*PSEUDO CLASS (after/before) ----*/ before,
    /*DIVIDER CONTENT ----------------*/  none,
    /*PROMO PADDING -----------------*/   0,
    /*IMAGE FLOAT:right, left, none  */   left,
    /*IMAGE WIDTH -------------------*/   100%,
    /*IMAGE PADDING -----------------*/   0px 0px 8px 0px,
    /*IMAGE HEIGHT (default 56.25%)--*/   120%,
    /*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
    /*METADATA PADDING --------------*/   0 0px,
    /*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);    
}
  
}}}

.silicon{
 margin-top: $list-item-top-margin;  
 width: 50%;
 float: left; 
    .promo__body{padding-bottom: 8px;}    
} 

.antimony{ 
width: 100%;
.antimony__list-item{
margin-top:  $list-item-top-margin; 

@include flex-basis( 33.333333333%);  
}
@include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/  '',
/*PROMO PADDING -----------------*/   0 0 0 0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 0px 8px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   8px 0px 0 0px,
/*METADATA PADDING --------------*/   0 0px,
/*SEE ALSO PADDING --------------*/   0px 0 0px 0px
);    
}   
   
    
}//END GROUP 3  
    
    
    
