// _mixins.scss
//

// quad__organism - controls the vertical spacing between organisms
@mixin quad__organism {
  margin-top:  32px;
  border-bottom: 0px solid $keyline;
}

//Organism container - controls container width and margins
@mixin organism__container{
&__container {
    margin-left: auto;
    margin-right: auto;
    @include mq($from:g1, $until:g2) {
        padding: 0;
    }
    @include mq($from:g2, $until:g3) {
        padding: 0 $g2-gutter-width / 2;
    }

     @include mq($from:g3, $until:g4) {

           @if $g3-gutter-width == 16px {

      padding: 0 8px;
            }
          @else if  $g3-gutter-width == 24px {
               padding: 0 4px;
        }
           @else {
               padding: 0 0px;
        }
    }

@include mq($from:g4, $until:g5) {

           @if $g4-gutter-width == 16px {

      padding: 0 8px;

            }
          @else if  $g4-gutter-width == 24px {
               padding: 0 4px;

        }
        @else if  $g4-gutter-width == 20px {
               padding: 0 8px;
        }
           @else {
               padding: 0 0px;
        }
    }

@include mq(1008px, 1279px) {
        max-width: 1008px;
       @if $g4-gutter-width == 16px {

      padding: 0 8px;
            }
          @else if  $g4-gutter-width == 24px {
               padding: 0 2px;
        }
          @else if  $g4-gutter-width == 20px {
               padding: 0 8px;
        }
           @else {
               padding: 0 0px;
        }

    }

@include mq(g5) {
        max-width: 1280px;
        @if $g5-gutter-width == 16px {

      padding: 0 4px;
            }
          @else if  $g5-gutter-width == 24px {
               padding: 0 4px;

        }
           @else if  $g5-gutter-width == 20px {
               padding: 0 4px;
        }
           @else {
               padding: 0 0px;
        }

    }
   @include mq($from: 600px, $until:650px) {
    padding: 0 $g2-gutter-width / 2;
  }

}
}



@mixin organism_first__item (
  $g3-PromoWidth,
  $g3-PromoImagePosition,
  $g3-PromoImagesize,
  $g3-imagePadding,
  $g5-PromoImagesize,
  $g5-PromoWidth,
  $g5-PromoImagePosition,
  $g5-imagePadding){
 &.first__item {
 h1 { @extend %double-pica, %great-primer--bold;}
   @include mq(g1) {
      width: calc(4.166666667% * 24);

     @include horizontal__promo(none, 1, 0);
   }
   @include mq(g3) {
     width: calc(4.166666667% * #{$g3-PromoWidth});
      margin-bottom: 0;
        // @mixin horizontal__promo($image-position, (100% / $image-width))
      @include horizontal__promo($g3-PromoImagePosition, $g3-PromoImagesize, $g3-imagePadding);
   }

   @include mq(g5) {
     width: calc(4.166666667% * #{$g5-PromoWidth});
      // @mixin horizontal__promo($image-position, (100% / $image-width))
      @include horizontal__promo($g5-PromoImagePosition, 1.5, $g5-imagePadding);
   }

       }
}

@mixin organism__item{
     position: relative;
     @include mq(g1){
       margin-top: $H-padding-top + $H-padding-top;


     }
    .promo {
      &:before {
      content: '';
      border-bottom: 1px solid $keyline;
      width: 100%;
      display: block;
      position: absolute;
      top: -12px;}
        .promo__label { display: inline-block; }

         @include mq(g3) {
                    .metadata {
                        clear: none;
                    }
                   }
                }

}

@mixin cldr-clearfix {

  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin cldr-nav {
  list-style: none;
  padding-left: 0;

  > li {

    a {
      display: block;
    }
  }
}

@mixin cldr-nav--inline {
  @include cldr-clearfix;

  > li {
    float: left;
  }
}

@mixin cldr-ff-monaco {
  font-family: monaco, Consolas, 'Lucida Console', monospace;
}
 @mixin cldr-nav--inline {
  @include cldr-clearfix;

  > li {
    float: left;
  }
}


// Horizontal Keyline
@mixin horizontal__keyline{
 &:before {
content: '';
position: absolute;
top: -12px;
left: 0;
right: 0;
height: 0.0625rem;
z-index: 2;
background-color: $keyline;
}
}

//001 - SECTION DIVIDERS
////001.1 AFTER
@mixin sectionAfterDivider(
  $DividerSelector,
  $DividerContent,
  $DividerTopPos,
  $DividerBottomPos,
  $DividerLeftPos,
  $DividerWidth
  ){
    &:#{$DividerSelector}{
         content:  $DividerContent;
         display: block;
         position: absolute;
         top:$DividerTopPos;
         bottom: $DividerBottomPos;
        left: $DividerLeftPos;
        width: $DividerWidth;
        border-top: 1px solid $keyline;
         }
}

@mixin promo-settings(
  $promoDividerSelector, $promoDividerContent,
   $promo-padding,
   $image-float,
   $image__width,
   $image__padding,
   $image__height,
   $promo__body-padding,
   $metadata__padding,
   $see-also__padding
  ){

    padding: $promo-padding;

    .promo__image-wrapper{
     position: relative;
     float: $image-float;
     width: $image__width;
     margin: $image__padding;
  }
   .promo__image-16x9{
     padding-bottom: $image__height;
     }

    .promo__body{

      padding: $promo__body-padding;
      }
     .metadata{
      display: table;
      padding: $metadata__padding;
     }
     .see-also{
      width: 100%;
      padding: $see-also__padding;
      margin-top: 0;
      display: inline-block;
      &:before{

      margin-bottom: 4px;
       }
      }
     .contributor{
      padding-bottom: 0;
     }
}

