.terbium{
overflow: hidden;
clear:both;
@include organism__container;
 margin-bottom: $slice-vertical-spacing;
 .terbium__content{
  margin-bottom: -$vertical-spacing;
 }

.terbium__list {
  display: flex;
	flex-flow: row wrap;
  }

.second-column{
  .terbium__list-item{
  	display: flex;
  	position: relative;
	}
}

.first-column{
  .promo__body{padding-bottom:$vertical-spacing;}
  .metadata{
    position: absolute;
    bottom:0;
   }
}

.terbium__list-item{
  .promo.lead{
      @include mq($from: g1, $until:g3){
        margin-bottom:$vertical-spacing;
      }
  }
  @include mq(g3){
  margin-bottom:$vertical-spacing;
  display: flex;
}
  }

.terbium__content{display: flex;}

@include mq($from: g1, $until:g3){
  .terbium__content{     flex-flow: column wrap;}
  .promo__image-wrapper{ position:relative; }
  .terbium__list-item{   @include flex-basis(100%);}
  .first-column{         width: 100%; float: left; flex-basis: 100%;order:2;}
  .second-column{        width: 100%; order:1;
  .terbium__list-item{   @include flex-basis(100%);}
 }
}//GROUP 1 & 2

@include mq($from: g3, $until:g4){
  .terbium__content{     flex-flow: column wrap;}
  .promo__image-wrapper{position:relative; margin-bottom:8px;}
  .terbium__list-item{ @include flex-basis(33.333%);}
  .first-column{ width: 100%; float: left; flex-basis: 100%;order:2;}
  .second-column{ width: 100%; order:1;
  .terbium__list-item{ @include flex-basis(100%);}
  }

}//GROUP 3


@include mq($from: g4, $until:g5){
.terbium__content{ display: flex;}
.terbium__list-item{
  @include flex-basis(33.333%);
  .promo__image-wrapper{position:relative; margin-bottom:8px;}
}
.first-column{ width: 60%; float: left; }
.second-column{
  width: 40%;

  .terbium__list-item{ @include flex-basis(100%);}
					}
	}//GROUP 4


@include mq(g5){
  .terbium__content{ display: flex;}
  .terbium__list-item{
  @include flex-basis(33.333%);
  .promo__image-wrapper{position:relative;}
}

.first-column{  width: 56.25%; float: left;}
.second-column{ width: 43.75%;
  .terbium__list-item{ @include flex-basis(100%); }
					}
    }//GROUP 5
}
