
// @include horizontal__promo(right, 1.5, 8px);
// displays horizontal or vertical promo with image left or right or vertical
// it also controls the badge position


.promo {
@include flex-flow(column wrap);
  display: flex;
  flex: 1 1 auto;
  padding-left: 0;
  padding-right: 0;
  .promo__image-wrapper{
    margin-bottom: 8px;
  }
  .promo__container{
    width: 100%;
  }
  .promo__body{
    padding-bottom:8px;
  }

  .promo__image-16x9{
    padding-bottom:56.25%;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  @include mq(g1) {
  margin-left: $g2-gutter-width / 2;
  margin-right: $g2-gutter-width / 2;
  .promo__container{position: relative;}
  .contributor__avatar{display: none;}
  }

  @include mq(g3) {
  margin-left: $g3-gutter-width / 2;
  margin-right: $g3-gutter-width / 2;
  .promo__container{position:initial;}
  .contributor__avatar{display: block;}
  }

  @include mq(g4) {
  margin-left: $g4-gutter-width / 2;
  margin-right: $g4-gutter-width / 2;
  }

  @include mq(g5) {
  margin-left: $g5-gutter-width / 2;
  margin-right: $g5-gutter-width / 2;
  }

  @include mq($from: 600px, $until:650px) {
  margin-left: $g2-gutter-width / 2;
  margin-right: $g2-gutter-width / 2;
  }



  h3{
  @extend %paragon, %paragon-bold;
    .promo__label{
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;
    top: 0px;
    font-weight: 700;
    -webkit-font-smoothing: auto;
    &.headline{ top: -2px;}
    }
  }

  h1{
    @extend %trafalgar, %trafalgar-pica--bold;
    letter-spacing: -1px;
     .promo__label{
      position: relative;
      font-size: 14px;
      line-height: 15px;
      top: -3px;
      letter-spacing: 0.5px;
      margin-top: 12px;
     }
  }

  p {
    @extend %long-primer;
    color: $summary-copy;
    margin-top: 8px;
    margin-bottom: 0px;
    display: table;
    -webkit-font-smoothing: antialiased;
  }
}

.promo--hero {

  @include mq(g4) {
    .promo__image-wrapper {
      float: right;
      width: 65.4%;
      margin-left: 12px;
      margin-bottom: 0;
    }
  }
  @include mq(g5) {
    .promo__image-wrapper {
      float: right;
      width: 50%;
      margin-left: 12px;
    }
  }
}

//Horizontal Promo group 1 to group 2
.promo-core--horizontal{
  @include mq($from:g1, $until:g2) {
    .promo__image-wrapper{
      width: 44.44%;
      float: left;
      margin-right: $g1-gutter-width;
      margin-bottom:0;
    }

    p{display:none;}
    .metadata{clear:both;}
  }

  @include mq($from:320px, $until:374px) {
     margin-bottom:24px;
    .contributor{
      display: inline-block;
    }
    .promo__body{padding-bottom:0;}

    .promo__image-wrapper{
      width: 132px;
      float: left;
      margin-right: $g2-gutter-width / 2;
      margin-bottom:0;
    }
    p{display:none;}
  }

  @include mq($from:374px, $until:549px) {
    margin-bottom:24px;
    .metadata {
      position: absolute;
      left: $g2-gutter-width / 2;
      margin-left: 152px;
      bottom: 0;
    }

    .promo__body{padding-bottom:$list-item-top-margin;}

    .promo__image-wrapper{
      width: 152px;
      float: left;
      margin-right: $g2-gutter-width / 2;
      margin-bottom:0;
    }
    p{display:none;}

  }

  @include mq($from:550px, $until:600px) {
     margin-bottom:24px;
    .promo__image-wrapper{
      width: 168px;
      float: left;
      margin-right: $g2-gutter-width / 2;
      margin-bottom:0;
    }
    .promo__body{padding-bottom:$list-item-top-margin;}
     .metadata {
      position: absolute;
      left: $g2-gutter-width / 2;
      margin-left: 168px;
      bottom: 0;
    }
  }
}


//Lead Promo Core Only

.silver .promo-core--lead .promo__body {padding-bottom:16px;}
.promo-core--lead{
   .promo__container{
    position: relative;
  }
  .index--alsos{

     li{margin-top:8px; display: inline-block; }

     @include mq($from:g4, $until:g5){
       margin-left: -12px;
       width: calc(100% + 24px);
      ul{width:100%;  clear: both;}
      li{
        width: 33.3333%;
        padding-left: 12px;
        padding-right: 12px;
        vertical-align: top;
      }
     }
     @include mq($from:440px, $until:g4){
       margin-left: -12px;
       width: calc(100% + 24px);
      ul{width:100%;  clear: both; margin-top:8px;}
      li{
        width: 50%;
        padding-left: 12px;
        padding-right: 12px;
        vertical-align: top;
      }
     }

    @include mq(g5) {
      position: absolute;
      bottom: 0;
      margin-left: -12px;
       width: calc(50% + 24px);
      ul{width:100%;  clear: both;}
      li{
        width: 50%;
        padding-left: 12px;
        padding-right: 12px;
        vertical-align: top;
      }

      }



  }
   @include mq($from:320px, $until:g3) {

  .promo__image-wrapper {
    overflow: hidden;
    float: none;
    width: calc(100% + #{$g2-gutter-width * 2});
    left: - $g2-gutter-width;
    position: relative;
  }
 }

 @include mq($from:g1, $until:g2) {
  .promo__image-wrapper {
    float: none;
    width: calc(100% + #{$g1-gutter-width * 2});
    left: - $g1-gutter-width;
    position: relative;
  }
 }
  @include mq($from:g1, $until:g3) {
  .promo__image-wrapper {
    margin-bottom: 8px;
  }
  }

}


.promo--headlines{
  .metadata{margin-top: $list-item-top-margin / 2;}
}
.promo.live{
  border-top:4px solid $news;
  padding-top: 8px;

  .promo__content{
    &:before{
    content: '';
    position: relative;
    z-index: 99;
    width: 100%;
    display: block;
    }
  }

  .live__content {
    display: block;
    margin-top: 8px;
    background-color: #F7F6F1;
    border-bottom: 2px solid #D8D0CB;
    padding: 1px 8px 8px 8px;
    margin-bottom: 24px;
    }

  p{
    strong{-webkit-font-smoothing: initial; color: #000;}
    }

  .promo__body {
    padding: 0px 0px 0px 0px !important;
    position: relative;
    }

  .metadata {
    color: #fff;
    bottom: 0;
    }

}



@include mq(g4){
.promo.advert { margin-top: -13px; }
}


.promo.promo--lead{
  .metadata{
    @include mq(g4){
    position:absolute;
    bottom:8px;
  }
  }

   @include mq($from:g3, $until:g4){
      .promo__image-wrapper{
       margin-bottom: 0px;
       width: 66.666%;
       float: left;
       margin-right: 8px;
    }
  }

  @include mq($from:g1, $until:g3){
      .promo__image-wrapper{
       margin-bottom: 8px;
    }
  }
}



.promo--card{
   position: relative;
  box-shadow: 0 2px 0px rgba(0,0,0,0.2);
  padding: 0 0 0 0;
  border-left: 1px solid #EAEAEA;
  border-right: 1px solid #eaeaea;
  background-color: #fff;
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;

  &:before {
    content: "";
    display: block;
    position: absolute;
    bottom: auto;
    background-color: #F9F9F9;
    height: calc(100% + 24px);
    z-index: -999;

  @include mq(g2){
    top: -$g2-gutter-width / 2;
    left:  - $g2-gutter-width;
    width: calc(100% + #{$g2-gutter-width * 2});
    }

  @include mq(g3){
    top: -$g3-gutter-width / 2;
    left: -$g3-gutter-width;
    width: calc(100% + #{$g3-gutter-width * 2});
    }

  @include mq(g4){
    top: -$g4-gutter-width / 2;
    left: -$g4-gutter-width / 2;
    width: calc(100% + #{$g4-gutter-width});
    }

  @include mq(g5){
    top: -$g5-gutter-width / 2;
    left: -$g5-gutter-width / 2;
    width: calc(100% + #{$g5-gutter-width});
    }
}

  .promo__body {padding: 0 16px;}
  .metadata{padding: 8px 16px 8px 16px;}
}


.promo__featured{
  background-color: #000;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  &.faux-block-link--hover{
    .faux-block-link__target a{ color: #ededed; text-decoration:underline;}
  }
  @include mq($from:g1, $until:g3) {
  padding-bottom:56.25%;
  margin-bottom: 12px;
  margin-left: -$g2-gutter-width / 2;
  margin-right: -$g2-gutter-width / 2;
  }

   @include mq(g3) {
  display: flex;
  }
  .promo__container{
      @include mq($from:g1, $until:g3) {
      position: absolute;
    bottom: 0;
  }
   }
  .promo__container:before{
    content: "";
    display: block;
    position: absolute;
    background-image:linear-gradient(to bottom,rgba(0,17,22,0) 0,rgba(0,17,22,.85) 100%);
    height: 56.5%;
    width: 100%;
    z-index: 6;
    bottom: 0;
     @include mq($from:g1, $until:g3) {
      height: 156px;
   }
  }
  .promo__image-wrapper{
    margin-bottom: 0;
    width: 100%;
  }

  .promo__image-16x9{
    padding-bottom:none;
  }
  .promo__body{
    width: 100%;
    padding: 0 8px;
    position:absolute;
    bottom: 12px;
    z-index: 10;
    @include mq(g3) {
      padding-bottom: 0px !important;
    }
    @include mq($from:g1, $until:g3) {
    padding-left: $g2-gutter-width;
    padding-right: $g2-gutter-width;
    }
    -webkit-font-smoothing: antialiased;
    a {color: #fff;}
    p {color:#fff; margin-top:0;}
    .metadata__time .icon svg { fill: #fff;}
    .metadata__time { color: #fff;}
  }
}

.lead .promo--lead.promo--solid-color{
    @include mq($from:g1, $until:g3){
    margin-left: 0px;
    margin-right: 0px;
    }
    @include mq($from:g4, $until:g5){
    padding-bottom:0px;
    .promo__body{
    padding-top:8px;
    }
    }
     @include mq(g5){
    padding-bottom:0px;
    }
  .promo__container{position:relative;}
   .metadata{
    padding-left:8px;
    padding-right:8px;
    padding-bottom:0;
  }
}

  .promo--solid-color{
    padding-bottom:8px;

    .promo__body{
    padding-left:8px;
    padding-right:8px;
    @include mq(g3){
    padding-bottom:32px;
  }
  }
  .metadata{
      padding-left:8px;
      padding-right:8px;
      padding-bottom:8px;
      @include mq(g4) {padding-top:8px;}
      @include mq($from:g1, $until:g3){
         padding-bottom:8px;
     }
    }
    .avatar{
    background-color: #fff;
  }
  background-color: #EFEFEF;

}

.promo-contributer{
  .contributor{
    padding-bottom: 8px;
  }
  .avatar{
   width: 56px;
  }
}
