// _typography-settings.scss
//

$typographic-scale-settings: (
    'double-trafalgar': (
        'g1': (
          'font-size': 30px,
          'line-height': 32px
        ),
        'g2': (
          'font-size': 32px,
          'line-height': 36px
        ),
        'g3': (
          'font-size': 48px,
          'line-height': 52px
        )
    ),
    'trafalgar': (
        'g1': (
          'font-size': 20px,
          'line-height': 24px
        ),
        'g2': (
          'font-size': 28px,
          'line-height': 32px
        ),
        'g3': (
          'font-size': 32px,
          'line-height': 36px
        ),
        'g4': (
          'font-size': 26px,
          'line-height': 30px
        ),
        'g5': (
          'font-size': 32px,
          'line-height': 36px
        )
    ),
    'double-pica': (
        'g1': (
          'font-size': 24px,
          'line-height': 28px,
          'letter-spacing':-1px
        ),
        'g3': (
          'font-size': 26px,
          'line-height': 30px,
          'letter-spacing':-0.8px
        )

    ),
    'great-primer': (
        'g1': (
          'font-size': 22px,
          'line-height': 28px
        ),
        'g3': (
          'font-size': 22px,
          'line-height': 26px,
          'letter-spacing':-0.5px
        )

    ),
     'paragon': (
        'g1': (
          'font-size': 16px,
          'line-height': 20px
        ),
       'g3': (
          'font-size': 18px,
          'line-height': 22px
        )
    ),
    'english': (
        'g1': (
          'font-size': 16px,
          'line-height': 20px,
          'letter-spacing': 0
        ),

        'g3': (
          'font-size': 18px,
          'line-height': 22px
        ),
        'g4': (
          'font-size': 16px,
          'line-height': 20px
        )
        ),
    'pica': (
        'g1': (
          'font-size': 15px,
          'line-height': 20px
        ),
        'g2': (
          'font-size': 16px,
          'line-height': 22px
        ),
        'g3': (
          'font-size': 18px,
          'line-height': 24px
        ),
        'g4': (
          'font-size': 16px,
          'line-height': 22px
        )
    ),
    'long-primer': (
        'g1': (
          'font-size': 14px,
          'line-height': 18px
        ),

    ),
    'brilliant': (
      'g1': (
        'font-size': 14px,
        'line-height': 18px,
        'font-weight': 400
      )
    ),
    'brevier': (
        'g1': (
          'font-size': 12px,
          'line-height': 12px
        ),
        'g4': (
          'font-size': 12px,
          'line-height': 12px
        )
    ),
    'minion': (
        'g1': (
          'font-size': 12px,
          'line-height': 16px,
          'text-transform': uppercase
        ),
        'g3': (
          'font-size': 13px
        ),
        'g4': (
          'font-size': 12px
        )
    )
) !default;

.trafalgar,.promo .trafalgar{
	@extend %trafalgar;
  letter-spacing: -1px;
}
.double-pica,.promo .double-pica{
	@extend %double-pica;
}
.brilliant{
	@extend %brilliant;
}
