
.standoutPromo{
   @include mq(g1) {
         @include promo-settings(
/*PSEUDO CLASS (after/before) ----*/ before,
/*DIVIDER CONTENT ----------------*/ '',
/*PROMO PADDING -----------------*/   0,
/*IMAGE FLOAT:right, left, none  */   none,
/*IMAGE WIDTH -------------------*/   100%,
/*IMAGE PADDING -----------------*/   0px 0px 0px 0px,
/*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
/*PROMO BODY PADDING ------------*/   0px 0px 0 0px,
/*METADATA PADDING --------------*/   0px 8px 8px 8px !important,
/*SEE ALSO PADDING --------------*/   8px 0 0px 0px
);
       .promo__content{
           background-color: #000;
       }
       .metadata {
    color: #fff;
        margin-top: 0px;

    .metadata__time{
      color: #fff;
      .icon svg {
        fill:#fff;
      }
    }
    .gs-o-live-status {
    color: #fff;
}
    .metadata__section{
      a{
      color: #fff;
      }
    }
}
    .image__wrap{
       position: relative;
       &:after{
         content: '';
         display: block;
         position: absolute;
         width: 100%;
         height: 100%;
         bottom: 0;
         z-index: 0;
         background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.95) 0, transparent 100%);
       }

     }
      h3{
          &:before{
                content: '';
    border-left: 2px solid #fff;
    display: block;
    left: -8px;
    position: absolute;
    height: 104%;
    top: 4px;


          }
   position: absolute;
   bottom: 16px;
       z-index: 99;
       color: #fff;
        margin-left: $g3-gutter-width / 2;
  margin-right: $g3-gutter-width / 2;
       }
  }
}






.promo.promo--standout-krypton{
  &:before{content: none;}
 @include mq($from:0px, $until:440px){

   @include promo-settings(
        /*PSEUDO CLASS (after/before) ---*/ before,
        /*DIVIDER CONTENT ---------------*/  none,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
        /*IMAGE WIDTH -------------------*/   100%,
        /*IMAGE PADDING -----------------*/   0px 0 0 0px,
        /*IMAGE HEIGHT (default 56.25%)--*/   80%,
        /*PROMO BODY PADDING ------------*/   0px $g2-gutter-width 8px $g2-gutter-width,
        /*METADATA PADDING --------------*/   0 $g2-gutter-width,
        /*SEE ALSO PADDING --------------*/   8px $g2-gutter-width 0px $g2-gutter-width
        );
  }
  @include mq($from:441px, $until:600px){

   @include promo-settings(
        /*PSEUDO CLASS (after/before) ---*/ before,
        /*DIVIDER CONTENT ---------------*/  none,
        /*PROMO PADDING -----------------*/   0,
        /*IMAGE FLOAT:right, left, none  */   none,
        /*IMAGE WIDTH -------------------*/   100%,
        /*IMAGE PADDING -----------------*/   0px 0 0 0px,
        /*IMAGE HEIGHT (default 56.25%)--*/   56.25%,
        /*PROMO BODY PADDING ------------*/   0px $g2-gutter-width 8px $g2-gutter-width,
        /*METADATA PADDING --------------*/   0 $g2-gutter-width,
        /*SEE ALSO PADDING --------------*/   8px $g2-gutter-width 0px $g2-gutter-width
        );


  }
  @include mq($from:g1, $until:g3){

margin-left: -$g2-gutter-width / 2;
margin-right: -$g2-gutter-width / 2;

      .promo__body{
  background-color: $standout--promo-bgcolor;
          p{display: block; color: $standout--promo-fgcolour; margin-top: 0; }
  }
      .metadata{
          background-color: $standout--promo-bgcolor;
    width: 100%;
    margin-top: 0;
    padding: 0px 16px 12px 16px;
    color: #fff;
          .metadata__time,.metadata__section a,.icon svg {
    color: $standout--promo-fgcolour;
              fill:$standout--promo-fgcolour;
}
      }

    }


   @include mq($from: 0px, $until:600px) {
     .image__wrap{
       position: relative;
       &:after{
         content: '';
         display: block;
         position: absolute;
         width: 100%;
         height: 100%;
         bottom: 0;
         z-index: 0;
         background-image: linear-gradient(0deg, rgba($standout--promo-gradient, 0.95) 0, transparent 100%);
       }

     }
     h1{
   position: absolute;
   bottom: 8px;
       z-index: 99;
       color: $standout--promo-h1;
        margin-left: $g3-gutter-width / 2;
  margin-right: $g3-gutter-width / 2;
       }
  }
}




